itions")]),_c('h4',[_vm._v("Blinded")]),_c('ul',[_c('li',[_vm._v("You cant see and automatically fail any ability check requiring sight.")]),_c('li',[_vm._v("Attack rolls against you have advantage and your attack rolls have disadvantage.")])]),_c('h4',[_vm._v("Charmed")]),_c('ul',[_c('li',[_vm._v("You cant attack the charmer or target the charmer with harmful abilities or magical effects.")]),_c('li',[_vm._v("The charmer has advantage on any ability check to socially interact with you.")])]),_c('h4',[_vm._v("Deafened")]),_c('ul',[_c('li',[_vm._v("You cant hear and automatically fail any ability check requiring hearing.")])]),_c('h4',[_vm._v("Cover")]),_c('ul',[_c('li',[_vm._v("Creature with Half Cover gets +2 to AC and Dex Saves.")]),_c('li',[_vm._v("3/4 Cover gets +5 to AC and dex saves.")]),_c('li',[_vm._v("Full cover cannot be targetted.")])]),_c('h4',[_vm._v("Frightened")]),_c('ul',[_c('li',[_vm._v("You have disadvantage on ability checks and attack rolls while the source of your fear is within line of sight.")]),_c('li',[_vm._v("You cannot willingly move closer to the source of your fear.")])]),_c('h4',[_vm._v("Grappled")]),_c('ul',[_c('li',[_vm._v("Your speed becomes 0 and you cannot benefit from any bonus to your speed.")]),_c('li',[_vm._v("\n          This condition ends if the grappler is incapacitated or if an effect removes you from the reach of the grappler or grappling effect.\n        ")])]),_c('h4',[_vm._v("Incapacitated")]),_c('ul',[_c('li',[_vm._v("You cannot take actions or reactions.")])]),_c('h4',[_vm._v("Invisible")]),_c('ul',[_c('li',[_vm._v("\n          You are impossible to see without the aid of magic or a special sense. For the purpose of hiding, you are heavily obscured. Your location can be detected by any noise you make or any tracks you leave.\n        ")]),_c('li',[_vm._v("\n          Attack rolls against you have disadvantage and your attack rolls have advantage.\n        ")])]),_c('h4',[_vm._v("Obscured/Concealed")]),_c('ul',[_c('li',[_vm._v("Lightly obscured includes dim light, patchy fog, moderate foliage, disadvantage on wisdom(perception) checks that rely on sight.")]),_c('li',[_vm._v("Heavily obscured areas--such as darkness, opaque fog or dense foliage--blocks  vision entirely, a creature in the area effectively is blind.")])]),_c('h4',[_vm._v("Paralyzed")]),_c('ul',[_c('li',[_vm._v("You are incapacitated and cannot move or speak.")]),_c('li',[_vm._v("You automatically fail Strength and Dexterity saving throws.")]),_c('li',[_vm._v("Attack rolls against you have advantage.")]),_c('li',[_vm._v("Any attack that hits you is a critical hit if your attacker is within 5 feet of you.")])]),_c('h4',[_vm._v("Petrified")]),_c('ul',[_c('li',[_vm._v("\n          You are transformed, along with any nonmagical object you are wearing or carrying, into a solid inanimate substance. Your weight increases by a factor of ten and you cease aging.\n        ")]),_c('li',[_vm._v("You are incapacitated, cannot move or speak, and are unaware of your surroundings.")]),_c('li',[_vm._v("Attack rolls against you have advantage.")]),_c('li',[_vm._v("You automatically fail Strength and Dexterity saving throws.")]),_c('li',[_vm._v("You are immune to poison and disease, though a poison or disease already in your system is suspended, not neutralized.")])]),_c('h4',[_vm._v("Poisoned")]),_c('ul',[_c('li',[_vm._v("You have disadvantage on attack rolls and ability checks.")])]),_c('h4',[_vm._v("Prone")]),_c('ul',[_c('li',[_vm._v("Your only movement option is to crawl, unless you stand up and thereby end this condition.")]),_c('li',[_vm._v("You have disadvantage on attack rolls.")]),_c('li',[_vm._v("\n          Attack rolls against you have advantage if your attacker is within 5 feet of you; otherwise, attack rolls against you have disadvantage.\n        ")])]),_c('h4',[_vm._v("Restrained")]),_c('ul',[_c('li',[_vm._v("Your speed becomes 0 and you cannot benefit from any bonus to your speed.")]),_c('li',[_vm._v("Attack rolls against you have advantage, and your attack rolls have disadvantage.")]),_c('li',[_vm._v("You have disadvantage on Dexterity saving throws.")])]),_c('h4',[_vm._v("Stunned")]),_c('ul',[_c('li',[_vm._v("You are incapacitated, cannot move, and can only speak falteringly.")]),_c('li',[_vm._v("You automatically fail Strength and Dexterity saving throws.")]),_c('li',[_vm._v("Attack rolls against you have advantage.")])]),_c('h4',[_vm._v("Unconscious")]),_c('ul',[_c('li',[_vm._v("You are incapacitated, cannot move or speak, and are unaware of your surroundings.")]),_c('li',[_vm._v("You drop whatever you were holding and fall prone.")]),_c('li',[_vm._v("You automatically fail Strength and Dexterity saving throws.")]),_c('li',[_vm._v("Attack rolls against you have advantage.")]),_c('li',[_vm._v("Any attack that hits you is a critical hit if your attacker is within 5 feet of you.")])])]),_c('b-tab',{attrs:{"title":"Injuries"}},[_c('h3',[_vm._v("Injuries")]),_c('b-tabs',{attrs:{"id":"injuries"}},[_c('b-tab',{attrs:{"title":"Rules"}},[_c('p',[_vm._v("\n            The following homebrew injury rules come via rypillal, with some minor changes for brevity and formatting. "),_c('a',{attrs:{"href":"http://homebrewery.naturalcrit.com/share/rypillall"}},[_vm._v("See his original at Homebrewery")]),_vm._v(".\n          ")]),_c('p',[_vm._v("\n            If the character is at 0 hit points or less, the character is injured, and the player rolls a d100 on the relevant Lingering Injuries table, adding the character's Constitution score to the roll. If the attack does enough damage that the leftover damage is equal to half or more of their maximum hit points, the character is severely injured, and the player doesn't add the character's Constitution score to the roll.\n          ")]),_c('p',[_vm._v("\n            Results should be written onto the character's sheet as a new flaw. Also, unless the result is \"I'm... I'm Alright!\", the character must still make death saving throws as usual. If a result doesn't specify any lasting injury to the character, assume that, in the long-term, they recover with no adverse lingering effects.\n          ")]),_c('p',[_vm._v("\n            Which Lingering Injuries table they roll on depends on the damage type of the attack that pushed the character to the 0 hit points threshold. If the injuring attack combined two or more damage types (such as slashing and fire damage), either go with the damage table that makes most sense, or roll once and cross reference the result on both (or all) tables, and go with what is most fun or interesting: the Dungeon Master has the final say. Should the character be resistant to the injuring attack's damage type, they gain advantage on the roll.\n          ")]),_c('h4',[_vm._v("Recovery")]),_c('p',[_vm._v("\n            Several of the Lingering Injury results give a base recovery time, often expressed in increments of days or weeks, after which the worst effects of an injury diminish. However, as different individuals recover at different rates, the base recovery times given in the various entries should be modified by deducting the character's Constitution score.\n          ")]),_c('p',[_vm._v("\n            As well as the character's general vigour, factors such as lifestyle can have an impact on how quickly a character can recuperate during downtime. Particularly bad living conditions can drag a recovery out for longer, whereas good food and surroundings can have a character up on their feet in no time!\n          ")]),_c('p',[_vm._v("\n            Consult the list below and, depending on the recovering character's lifestyle, apply a positive or negative adjustment to the character's effective Constitution score for recovery purposes. If a character undergoes a variety of lifestyles while recuperating, average out the modifier according to logic.\n          ")]),_c('p',[_vm._v("\n            Additionally, the character can choose to focus on recuperating during their downtime. Each whole day of recuperating activity counts as two days of base recovery time.\n          ")])]),_c('b-tab',{attrs:{"index":"1","title":"Acid"}},[_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("D100 Roll")]),_c('th',[_vm._v("Injury")]),_c('th',[_vm._v("Description")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("1")]),_c('th',[_vm._v("Instant Death.")]),_c('td',[_vm._v("Your brain or other major organs are dissolved, killing you instantly.")])]),_c('tr',[_c('td',[_vm._v("2")]),_c('th',[_vm._v("Fatal Wound")]),_c('td',[_vm._v("Either you suffer massive tissue damage, or major organs are part eaten away. Even if you succeed at the normal death saving throws, you remain unconscious and will die after 1d6 turns have passed, at the end of your turn, unless you are magically healed above 0 hit points, or stabilised with a DC 17 Wisdom (Medicine) check using a healer's kit.")])]),_c('tr',[_c('td',[_vm._v("3")]),_c('th',[_vm._v("Paralysis")]),_c('td',[_vm._v("Spinal damage leaves you unable to move or feel anything from the waist down. You will need carrying or some sort of device to allow mobility (otherwise you can only crawl 5 feet per turn), and count as restrained. Still, there is always hope for a particularly strong person: at the beginning of every week, you can make a DC 20 Constitution saving throw  on a success, you miraculously recover. Otherwise, this flaw only ends if you receive powerful magic such as Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("4")]),_c('th',[_vm._v("Melted Arm")]),_c('td',[_vm._v("Your arm or hand is scorched off, pumping arterial blood and yellow ichor everywhere. Due to the flow of blood, you lose 1d6 hit points at the end of each of your turns, unless you receive magical healing to seal the stump, it is cauterised by fire or radiant damage, or another character uses an action to apply a tourniquet. Due to system shock, you must make a death saving throw immediately, with disadvantage on the roll. Following recovery, you can no longer hold anything with two hands, and can hold only a single object at a time. The distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. Finally, if the lost arm was your dominant arm (50% chance), unless you are trained in the Dual Wielder feat or the Two-Weapon Fighting Style, you suffer disadvantage with weapon attacks until you learn to use your off-hand. You learn to live with this pain and train your off-hand after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("5")]),_c('th',[_vm._v("Melted Leg")]),_c('td',[_vm._v("Your leg or foot is scorched off, pumping arterial blood and yellow ichor everywhere. Due to the flow of blood, you lose 1d6 hit points at the end of each of your turns, unless you receive magical healing to seal the stump, it is cauterised by fire or radiant damage, or another character uses an action to apply a tourniquet. Due to system shock, you must make a death saving throw immediately, with disadvantage on the roll. Following recovery, your speed on foot is halved and you must use a cane or crutch to move, unless you receive a prosthesis of some kind. Additionally, you fall prone after using the dash action, and suffer disadvantage on Dexterity checks made to balance. Finally, distraction from the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. You learn to live with this pain after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("6-11")]),_c('th',[_vm._v("Multiple Injuries")]),_c('td',[_vm._v("Roll again on this table 1d3+1 times, suffering the combined effects of the various results (within reason!). Reroll any duplicates, and also reroll Multiple Injuries, Minor Scar, Full Recovery, I'm... I'm Alright! and any results which indicate death (like Fatal Wound or Instant Death).")])]),_c('tr',[_c('td',[_vm._v("12-14")]),_c('th',[_vm._v("Internal Bleeding")]),_c('td',[_vm._v("The attack leaves you with hidden damage, drawing you towards death. You lose 1d3 hit points at the end of every hour, even if stabilised. This injury is difficult to spot, but once diagnosed, This flaw can be removed by magic such as Heal, Regenerate or Wish, or if another character proficient in Medicine conducts surgery: this takes 1 hour, expends one use of a healer's kit and requires a successful DC 17 Wisdom (Medicine) check. The surgery itself inflicts 3d6 piercing damage.")])]),_c('tr',[_c('td',[_vm._v("15-17")]),_c('th',[_vm._v("Frail")]),_c('td',[_vm._v("You recover, but your health is never quite the same again. You can barely keep food down, and lose 1 point from your Constitution score. Finally, if you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce the character's hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("18-20")]),_c('th',[_vm._v("Painful Joints")]),_c('td',[_vm._v("You heal, but are never again as sprightly. You lose 1 point from your Dexterity score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("21-23")]),_c('th',[_vm._v("Enfeebled")]),_c('td',[_vm._v("You heal, but are never again as strong. You lose 1 point from your Strength score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("24-27")]),_c('th',[_vm._v("Ruined Eye")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 17 Dexterity saving throw  if you fail (or have no helmet), one of your eyes is destroyed. From now on, you suffer disadvantage on Wisdom (Perception) checks that rely on sight, and on ranged attack rolls. If a subsequent injury damages your other eye, you are permanently blinded. Magic such as Regenerate or Wish can restore the lost eye.")])]),_c('tr',[_c('td',[_vm._v("28-30")]),_c('th',[_vm._v("Pelvic Injury")]),_c('td',[_vm._v("The attack destroys your nethers. Whenever you attempt a Strength (Athletics) check or take any action in combat (other than casting a spell), you must succeed at a DC 11 Constitution saving throw or lose 1d6 hit points. You also can't ride a horse without suffering 1d10 hit points. Finally, this injury renders you permanently infertile. This flaw ends after a base recovery time of 70 days (see Recovery above), or if you receive magic such as Heal, Regenerate or Wish, although the infertility can only be reversed by Greater Restoration or Remove Curse.")])]),_c('tr',[_c('td',[_vm._v("31-35")]),_c('th',[_vm._v("Leg Wound")]),_c('td',[_vm._v("Your leg is badly damaged. Until a base recovery time of 40 days has passed (see Recovery above), you must use a cane or crutch to move at half speed, you fall prone after using the dash action, and you suffer disadvantage on Dexterity checks made to balance. Even after the worst of the damage has passed, your speed on foot is reduced by 5 feet. The limb can be restored by either Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("36-39")]),_c('th',[_vm._v("Hand Injury")]),_c('td',[_vm._v("The attack costs you several fingers. There is a 50% chance of the affected hand being your dominant hand. You can just about carry a shield, but if it's a weapon hand, your weapon attacks suffer a -2 penalty, unless you are trained in either the Dual Wielder feat or the Two-Weapon Fighting Style. Also, if it is your dominant hand, if you attempt to cast a spell with somatic components, you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. These penalties last a base recovery time of 40 days (see Recovery above). This flaw can be reversed by magic such as Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("40-44")]),_c('th',[_vm._v("Ruined Armor")]),_c('td',[_vm._v("Unless your armour is enchanted or made of adamantine, the attack leaves it badly damaged, affording little protection and fitting badly. You suffer -2 from your AC. This penalty lasts until it is replaced, repaired (costing half the the total cost of the item) or subject to a Mending spell. If you have no armour, treat this result as Minor Scar instead.")])]),_c('tr',[_c('td',[_vm._v("45-48")]),_c('th',[_vm._v("Old Battle Wound")]),_c('td',[_vm._v("You more or less recover, but become plagued for years with recurring pains. After every long rest, you must succeed at a DC 11 Constitution saving throw or suffer -2 to your Dexterity and Wisdom scores until the next long rest. This penalty can be neutralised by special salves, prepared by a character proficient in Medicine or using a herbalism kit with which they are proficient. This flaw can be removed through magic such as Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("49-52")]),_c('th',[_vm._v("Useless Shield")]),_c('td',[_vm._v("Unless your shield is enchanted or made of adamantine, the attack leaves it ruined and worthless. If you don't have a shield, treat this result as Ruined Armour instead.")])]),_c('tr',[_c('td',[_vm._v("53-59")]),_c('th',[_vm._v("Infected Wounds")]),_c('td',[_vm._v("The damage refuses to properly heal, and instead becomes swollen and leaks a noisome pus. Your hit point maximum is reduced by 1 for every 24 hours the wound persists. If your hit point maximum drops to 0, you die. The wound heals if you receive magical healing or, alternatively, someone can tend to the wound and make a DC 14 Wisdom (Medicine) check once every 24 hours. After ten successes, the wound heals. Characters who are immune to disease treat this result as Minor Scar instead.")])]),_c('tr',[_c('td',[_vm._v("60-62")]),_c('th',[_vm._v("Personality Change")]),_c('td',[_vm._v("In the aftermath of the attack, due to post-traumatic stress, you soon exhibit a shift in personality. Roll 1d100 and consult the Indefinite Madness table (on page 260 of the Dungeon Master's Guide) to see what permanent flaw you gain. This flaw can only be removed by magic such as Greater Restoration or Wish, or possibly by some kind of long-term therapy, at the Dungeon Master's discretion.")])]),_c('tr',[_c('td',[_vm._v("63-65")]),_c('th',[_vm._v("I'm Melting!")]),_c('td',[_vm._v("Unless you are immune to being frightened, you frantically stumble towards an ally, grasping at them, pleading for help and dripping acid, before passing out. You immediately move up to half your speed (rounded up) towards a random friendly creature and attempt a grapple; this movement can result in opportunity attacks. If the grapple succeeds, you also inflict 1d6 acid damage. At the beginning of your next turn, you fall unconscious, and begin making death saving throws as usual.")])]),_c('tr',[_c('td',[_vm._v("66-69")]),_c('th',[_vm._v("Fixation")]),_c('td',[_vm._v("Following recovery, you develop an unhealthy fixation. Roll 1d6: on a 1-3, you nurture a bitter enmity; on a 4-6, you develop an overpowering phobia. If you are immune to being frightened, then this result is bitter enmity. You should work with the Dungeon Master on the phrasing of the new flaw: your hatred or phobia may focus on some element of the attacker's identity (for example, a mindless hatred towards dragons or sorcerers) or even the type of attack (such as a fear of acid). This flaw can be removed by magic such as Greater restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("70-76")]),_c('th',[_vm._v("Horrible Scar")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 14 Dexterity saving throw  if you fail (or have no helmet), you are disfigured to the extent that the wound can't be easily concealed, or palated. You have disadvantage on Charisma (Persuasion) checks and advantage on Charisma (Intimidation) checks. Magic like Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("77-96")]),_c('th',[_vm._v("Minor Scar")]),_c('td',[_vm._v("The scar doesn't have any adverse effect, but may make you more memorable and badass! Magic such as Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("97-98")]),_c('th',[_vm._v("Scarred Nerves")]),_c('td',[_vm._v("Bathed in a mist of acid droplets, the pain is so intense it proves transcendental, ruining almost all your nerve endings. Although horrific, you learn to live with the background pain, and future sensations seem trivial. Following a long rest, you feel almost no pain, and can reroll a failed Constitution saving throw to maintain concentration on spells due to receiving damage; you must accept the second result. Also, in future, when you are reduced to 0 hit points but not killed outright, you can drop to 1 hit point instead. You can't use this feature again until you finish a long rest. Finally, as you may find it difficult to perceive the extent of your injuries, the Dungeon Master might like to roll for your future Lingering Injuries in secret! This flaw can only be removed by magic such as Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("99+")]),_c('th',[_vm._v("I'm... I'm Alright!")]),_c('td',[_vm._v("The gods must be smiling today  the blow seemed worse than it actually is, and you are assumed dead. Rather than making any death saving throws, you are instead stabilised and returned to 1 hit point and, at the beginning of your next turn, regain 1d10 hit points and can act as normal, gaining advantage on your first attack that turn.")])])])])]),_c('b-tab',{attrs:{"index":"2","title":"Bludgeoning"}},[_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("D100 Roll")]),_c('th',[_vm._v("Injury")]),_c('th',[_vm._v("Description")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("1")]),_c('th',[_vm._v("Instant Death.")]),_c('td',[_vm._v("Your skull is crushed, or you suffer massive internal injuries, killing you instantly.")])]),_c('tr',[_c('td',[_vm._v("2")]),_c('th',[_vm._v("Fatal Wound")]),_c('td',[_vm._v("Either your back is broken or your internal organs are ruptured. Even if you succeed at the normal death saving throws, you remain unconscious and will die after 1d6 turns have passed, at the end of your turn, unless you are magically healed above 0 hit points, or stabilised with a DC 17 Wisdom (Medicine) check using a healer's kit.")])]),_c('tr',[_c('td',[_vm._v("3")]),_c('th',[_vm._v("Paralysis")]),_c('td',[_vm._v("Spinal damage leaves you unable to move or feel anything from the waist down. You will need carrying or some sort of device to allow mobility (otherwise you can only crawl 5 feet per turn), and count as restrained. Still, there is always hope for a particularly strong person: at the beginning of every week, you can make a DC 20 Constitution saving throw  on a success, you miraculously recover. Otherwise, this flaw only ends if you receive magic such as Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("4")]),_c('th',[_vm._v("Shattered Arm")]),_c('td',[_vm._v("Your arm or hand is smashed beyond recovery, and will require amputation. Following recovery, you can no longer hold anything with two hands, and can hold only a single object at a time. The distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. Finally, if the lost arm was your dominant arm (50% chance), unless you are trained in the Dual Wielder feat or the Two-Weapon Fighting Style, you suffer disadvantage with weapon attacks until you learn to use your off-hand. You learn to live with this pain and train your off-hand after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("5")]),_c('th',[_vm._v("Shattered Leg")]),_c('td',[_vm._v("Your leg or foot is smashed beyond recovery, and will require amputation. Following recovery, your speed on foot is halved and you must use a cane or crutch to move, unless you receive a prosthesis of some kind. Additionally, you fall prone after using the dash action, and suffer disadvantage on Dexterity checks made to balance. Finally, the distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. You learn to live with this pain after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("6-11")]),_c('th',[_vm._v("Multiple Injuries")]),_c('td',[_vm._v("Roll again on this table 1d3+1 times, suffering the combined effects of the various results (within reason!). Reroll any duplicates, and also reroll Multiple Injuries, Minor Scar, Full Recovery, \"I'm... I'm Alright!\" and any results which indicate death (like Fatal Wound or Instant Death).")])]),_c('tr',[_c('td',[_vm._v("12-13")]),_c('th',[_vm._v("I Can't Move!")]),_c('td',[_vm._v("Brain trauma leaves you fully paralysed, at least for a short time. You can do little more than speak, but can still cast spells that don't require somatic components. However, as you can't feel much of your body, you gain advantage on any Constitution saving throw to maintain concentration on spells due to receiving damage. After every long rest, the character can make a DC 14 Constitution saving throw to end the paralysis. This flaw can also be removed by magic such as Freedom of Movement, Greater Restoration, Lesser Restoration, Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("14-15")]),_c('th',[_vm._v("I Can't See!")]),_c('td',[_vm._v(" If you wear a helmet, you must make a DC 11 Constitution saving throw  if you fail (or have no helmet), then one too many blows to the head has damaged your brain in a strange and horrible way. You are temporarily blinded. This flaw ends when you receive magic such as Lesser Restoration, Greater Restoration, Heal, Regenerate or Wish, or you succeed at a DC 14 Constitution saving throw, which you can make after every long rest.")])]),_c('tr',[_c('td',[_vm._v("16-19")]),_c('th',[_vm._v("Internal Bleeding")]),_c('td',[_vm._v("The attack leaves you with hidden damage, drawing you towards death. You lose 1d3 hit points at the end of every hour, even if stabilised. This injury is difficult to spot, but once diagnosed, This flaw can be removed by magic such as Heal, Regenerate or Wish, or if another character proficient in Medicine conducts surgery: this takes 1 hour, expends one use of a healer's kit and requires a successful DC 17 Wisdom (Medicine) check. The surgery itself inflicts 3d6 piercing damage.")])]),_c('tr',[_c('td',[_vm._v("20-22")]),_c('th',[_vm._v("Frail")]),_c('td',[_vm._v("You recover, but your health is never quite the same again. You can barely keep food down, and lose 1 point from your Constitution score. Finally, if you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce the character's hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("23-25")]),_c('th',[_vm._v("Painful Joints")]),_c('td',[_vm._v("You heal, but are never again as sprightly. You lose 1 point from your Dexterity score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("26-28")]),_c('th',[_vm._v("Enfeebled")]),_c('td',[_vm._v("You heal, but are never again as strong. You lose 1 point from your Strength score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("29-31")]),_c('th',[_vm._v("Head Injury")]),_c('td',[_vm._v(" If you wear a helmet, you must make a DC 11 Constitution saving throw  if you fail (or have no helmet), then head trauma damages your mental facilities somehow, often leaving a bald patch at the scar. Roll 1d3: 1: you lose 1 point from your Intelligence score; 2: you lose 1 point from your Wisdom score; 3: you lose 1 point from your Charisma score. This flaw is often accompanied by speech or memory problems, requiring you to relearn basic skills and social niceties. If the same ability score is affected a second time or more in the future by this flaw, you must succeed at a DC 14 Constitution saving throw or also suffer the Coma flaw (see the Cold Damage page). This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("32-35")]),_c('th',[_vm._v("Multiple Fractures")]),_c('td',[_vm._v("You take a thorough beating, punctuated by the cracking of several bones. Roll 1d6 twice (rerolling duplicates), and you suffer both the relevant flaws: 1: Broken Arm; 2: Broken Jaw; 3: Broken Ribs; 4: Hand Injury; 5: Leg Wound; 6: Pelvic Injury.")])]),_c('tr',[_c('td',[_vm._v("36-39")]),_c('th',[_vm._v("Broken Jaw")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 11 Dexterity or Constitution saving throw (your choice)  if you fail (or have no helmet), your jaw is smashed, scattering teeth everwhere. You find eating and speaking very painful, and suffer disadvantage on most Charisma checks. Additionally, if you attempt to cast a spell which uses verbal components, you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. This flaw ends after a base recovery time of 90 days (see Recovery above), or if you receive magic such as Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("40-43")]),_c('th',[_vm._v("Broken Arm")]),_c('td',[_vm._v("As the attack slams home, an audible 'crack' renders an arm close to useless for a time. There is a 50% chance that the broken arm is your dominant arm and if so, unless you are trained in the Dual Wielder feat or the Two-Weapon Fighting Style, you suffer disadvantage with weapon attacks. If it is your other arm, you cannot wear a shield. This flaw ends after a base recovery time of 30 weeks (see Recovery above), or if you receive magic such as a Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("44-47")]),_c('th',[_vm._v("Deafened")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 14 Constitution saving throw  if you fail (or have no helmet), then either through brain damage or damage to the ears, you are deafened. This flaw is removed if you receive magic such as Greater Restoration, Heal, Lesser Restoration, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("48-51")]),_c('th',[_vm._v("Pelvic Injury")]),_c('td',[_vm._v("Either your pelvis is struck a crippling blow, or the attack destroys your nethers. Whenever you attempt a Strength (Athletics) check or take any action in combat (other than casting a spell), you must succeed at a DC 11 Constitution saving throw or lose 1d6 hit points. You also can't ride a horse without suffering 1d10 hit points. Finally, this injury renders you permanently infertile. This flaw ends after a base recovery time of 70 days (see Recovery above), or if you receive magic such as Heal, Regenerate or Wish, although the infertility can only be reversed by Greater Restoration or Remove Curse.")])]),_c('tr',[_c('td',[_vm._v("52-55")]),_c('th',[_vm._v("Leg Wound")]),_c('td',[_vm._v("Your leg is badly damaged. Until a base recovery time of 40 days has passed (see Recovery above), you must use a cane or crutch to move at half speed, you fall prone after using the dash action, and you suffer disadvantage on Dexterity checks made to balance. Even after the worst of the damage has passed, your speed on foot is reduced by 5 feet. The limb can be restored by magic such as Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("56-59")]),_c('th',[_vm._v("Broken Ribs")]),_c('td',[_vm._v("Your rib cage is partly caved in. Whenever you attempt a Strength (Athletics) check or attempt to take any action in combat (other than casting a spell), you must succeed at a DC 11 Constitution saving throw. If you fail the save, you lose your action and can't use reactions until the start of your next turn. This flaw ends after a base recovery time of 50 days (see Recovery above), or if you receive magic such as Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("60-63")]),_c('th',[_vm._v("Punch Drunk")]),_c('td',[_vm._v(" If you wear a helmet, you must make a DC 11 Constitution saving throw  if you fail (or have no helmet), then due to hearing impairment or brain trauma, your coordination is often disrupted. After every short or long rest, you must succeed at a DC 11 Constitution or Wisdom saving throw (your choice), or suffer disadvantage on Dexterity (Acrobatics) checks. This penalty lasts until the next short or long rest. Additionally, if you are knocked prone, it costs you double the standard movement to stand up. This flaw can be removed through magic such as Greater Restoration, Heal, Lesser Restoration, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("64-67")]),_c('th',[_vm._v("Ruined Armor")]),_c('td',[_vm._v("Unless your armour is enchanted or made of adamantine, the attack leaves it badly damaged, affording little protection and fitting badly. You suffer -2 from your AC. This penalty lasts until it is replaced, repaired (costing half the the total cost of the item) or subject to a Mending spell. If you have no armour, treat this result as Minor Scar instead.")])]),_c('tr',[_c('td',[_vm._v("68-71")]),_c('th',[_vm._v("Useless Shield")]),_c('td',[_vm._v("Unless your shield is enchanted or made of adamantine, the attack leaves it ruined and worthless. If you don't have a shield, treat this result as Ruined Armour instead.")])]),_c('tr',[_c('td',[_vm._v("72-74")]),_c('th',[_vm._v("Personality Change")]),_c('td',[_vm._v("In the aftermath of the attack, either as a result of head injury or due to post-traumatic stress, you soon exhibit a shift in personality. Roll 1d100 and consult the Indefinite Madness table (on page 260 of the Dungeon Master's Guide) to see what permanent flaw you gain. This flaw can only be removed by magic such as Greater Restoration or Wish, or possibly by some kind of long-term therapy, at the Dungeon Master's discretion.")])]),_c('tr',[_c('td',[_vm._v("75-76")]),_c('th',[_vm._v("Fastball Special")]),_c('td',[_vm._v("The force of the attack sends you haplessly flying through the air towards another unsuspecting creature. You are pushed up to 10 feet towards a random creature and, if in range to strike it, knock it prone, unless that other creature succeeds at a DC 11 Dexterity or Strength saving throw (creature's choice). This roll is made with disadvantage if you are bigger than the dodging creature. If you are huge or gargantuan, you are too heavy to be thrown, and this result has no effect.")])]),_c('tr',[_c('td',[_vm._v("77-98")]),_c('th',[_vm._v("Minor Scar")]),_c('td',[_vm._v("The scar doesn't have any adverse effect, but may make you more memorable and badass! Magic such as Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("99+")]),_c('th',[_vm._v("I'm... I'm Alright!")]),_c('td',[_vm._v("The gods must be smiling today  the blow seemed worse than it actually is, and you are assumed dead. Rather than making any death saving throws, you are instead stabilised and returned to 1 hit point and, at the beginning of your next turn, regain 1d10 hit points and can act as normal, gaining advantage on your first attack that turn.")])])])])]),_c('b-tab',{attrs:{"index":"3","title":"Cold"}},[_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("D100 Roll")]),_c('th',[_vm._v("Injury")]),_c('th',[_vm._v("Description")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("1")]),_c('th',[_vm._v("Shattered Pieces")]),_c('td',[_vm._v("You are frozen solid, and the impact as you hit the floor shatters you into hundreds of pieces. You can now only be brought back by powerful magic like Reincarnate, True Resurrection or Wish.")])]),_c('tr',[_c('td',[_vm._v("2")]),_c('th',[_vm._v("Instant Death.")]),_c('td',[_vm._v("You are frozen solid, dying instantly.")])]),_c('tr',[_c('td',[_vm._v("3")]),_c('th',[_vm._v("Fatal Wound")]),_c('td',[_vm._v("Either you suffer massive tissue damage, or your major organs are necrotised by the cold. Even if you succeed at the normal death saving throws, you remain unconscious and will die after 1d6 turns have passed, at the end of your turn, unless you are magically healed above 0 hit points, or stabilised with a DC 17 Wisdom (Medicine) check using a healer's kit.")])]),_c('tr',[_c('td',[_vm._v("4")]),_c('th',[_vm._v("Coma")]),_c('td',[_vm._v("You enter a death-like sleep from which nothing can seemingly awaken you, though dim life signs still remain. You remain unconscious, even after being stabilised. Following a long rest, make a death saving throw. Three failed saves result in death (possibly due to lack of fluids, amongst other complications), but a result of 15 or means the coma ends. If and when you awaken, you suffer three levels of exhaustion (these exhaustion levels can be removed normally). The coma can also be ended prematurely by magic such as Dream, Remove Curse, Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("5")]),_c('th',[_vm._v("Destroyed Arm")]),_c('td',[_vm._v("Your arm or hand is frozen solid and then shattered, leaving only a numb, blackened stump. Due to system shock, you must make a death saving throw immediately, with disadvantage on the roll. Following recovery, you can no longer hold anything with two hands, and can hold only a single object at a time. Additionally, if the lost arm was your dominant arm (50% chance), unless you are trained in the Dual Wielder feat or the Two-Weapon Fighting Style, you suffer disadvantage with weapon attacks until you learn to use your off-hand. You learn to train your off-hand after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("6")]),_c('th',[_vm._v("Destroyed Leg")]),_c('td',[_vm._v("Your leg or foot is frozen solid and then shattered, leaving only a numb, blackened stump. Due to system shock, you must make a death saving throw immediately, with disadvantage on the roll. Following recovery, your speed on foot is halved and you must use a cane or crutch to move, unless you receive a prosthesis of some kind. Additionally, you fall prone after using the dash action, and suffer disadvantage on Dexterity checks made to balance. The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("7-11")]),_c('th',[_vm._v("Multiple Injuries")]),_c('td',[_vm._v("Roll again on this table 1d3+1 times, suffering the combined effects of the various results (within reason!). Reroll any duplicates, and also reroll Multiple Injuries, Minor Scar, Full Recovery, \"I'm... I'm Alright!\" and any results which indicate death (like Fatal Wound, Instant Death or worse).")])]),_c('tr',[_c('td',[_vm._v("12-15")]),_c('th',[_vm._v("Lung Trauma")]),_c('td',[_vm._v("Your lungs are withered, placing you dangerously close to death. While this flaw remains, you can only stabilise naturally after five death saving throws, rather than three. Following recovery, even routine activity leaves you breathless, and you are wracked with coughing fits when the air is thin or smoky. For every four hours spent walking (or eight hours riding), and when reduced to half your maximum hit points or less, you must succeed at a DC 11 Constitution saving throw or suffer one level of exhaustion. Exhaustion levels gained from this flaw are lost during a short or long rest, or can be removed by a character equipped with a herbalism kit with which they are proficient. Finally, this flaw ends if you succeed at a DC 17 Constitution saving throw rolled at the beginning of every month, or if removed by Heal, Greater Restoration, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("16-19")]),_c('th',[_vm._v("Severe Hypothermia")]),_c('td',[_vm._v(" Reduced core body heat leaves you with pale puffy skin, blue extremities and lips, and confusion. Your speed is halved (rounded up) and, in combat, you count as if you are subject to the confusion spell (although you cannot make Wisdom saving throws to end the effect as in the standard spell). This flaw ends if you succeed at a DC 11 Constitution saving throw rolled after a long rest, or you receive magic such as Greater Restoration, Protection from Energy (cold) or Wish. At the Dungeon Master's discretion, sources of intense heat may allow you to make an additional saving throw.")])]),_c('tr',[_c('td',[_vm._v("20-23")]),_c('th',[_vm._v("Frail")]),_c('td',[_vm._v("You recover, but your health is never quite the same again. You can barely keep food down, and lose 1 point from your Constitution score. Finally, if you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce the character's hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("24-27")]),_c('th',[_vm._v("Painful Joints")]),_c('td',[_vm._v("You heal, but are never again as sprightly. You lose 1 point from your Dexterity score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("28-31")]),_c('th',[_vm._v("Enfeebled")]),_c('td',[_vm._v("You heal, but are never again as strong. You lose 1 point from your Strength score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("32-36")]),_c('th',[_vm._v("Crippling Fatigue")]),_c('td',[_vm._v("You can never shake your sense of tiredness, and gain one level of exhaustion. This can be removed normally. However, following every long rest, you must succeed at a DC 11 Constitution or Wisdom saving throw (your choice), or gain a level of exhaustion. If you fail this saving throw by 5 or more, you gain two levels. Accumulating enough exhaustion levels can lead to your death. However, if you roll an unmodified 20, this flaw ends, and can also be ended by magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("37-41")]),_c('th',[_vm._v("Frost Bite")]),_c('td',[_vm._v(" Some of your digits rot away, leaving painful blackened stumps. Roll 1d3: 1: you lose 1d3 toes; 2: you lose 1d3 fingers; 3: you lose 1d3 fingers and 1d3 toes. If you lose toes, your foot speed is reduced by 5 feet. If you lose fingers, there is a 50% chance of the affected hand being your dominant hand. You can just about carry a shield, but if it's a weapon hand, your weapon attacks suffer a -2 penalty, unless you are trained in either the Dual Wielder feat or the Two-Weapon Fighting Style. Also, if it is your dominant hand, if you attempt to cast a spell with somatic components, you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. These penalties last a base recovery time of 40 days (see Recovery above). This flaw can be removed by magic such as Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("42-46")]),_c('th',[_vm._v("Deep Freeze")]),_c('td',[_vm._v("Shaking violently with the cold, your strength is sapped for days. While affected by this flaw, you only recover a quarter of your hit dice (rounded up) during a long rest. After a long rest, you can make a DC 17 Constitution saving throw (with advantage if resistant to cold damage): on a success, this flaw ends  otherwise, it remains until you succeed on a future roll, or until you are the subject of magic such as Heal, Greater Restoration, Protection from Energy (cold) or Wish.")])]),_c('tr',[_c('td',[_vm._v("47-50")]),_c('th',[_vm._v("Ruined Armor")]),_c('td',[_vm._v("Unless your armour is enchanted or made of adamantine, the attack leaves it badly damaged, affording little protection and fitting badly. You suffer -2 from your AC. This penalty lasts until it is replaced, repaired (costing half the the total cost of the item) or subject to a Mending spell. If you have no armour, treat this result as Minor Scar instead.")])]),_c('tr',[_c('td',[_vm._v("51-64")]),_c('th',[_vm._v("Useless Shield")]),_c('td',[_vm._v("Unless your shield is enchanted or made of adamantine, the attack leaves it ruined and worthless. If you don't have a shield, treat this result as Ruined Armour instead.")])]),_c('tr',[_c('td',[_vm._v("65-68")]),_c('th',[_vm._v("Wave of Ice")]),_c('td',[_vm._v("A freak burst of elemental ice blossoms where you fall. All other creatures within 10-foot range of you must succeed on a DC 11 Constitution saving throw, or suffer 2d6 cold damage. Additionally, this 10 feet radius of floor is frozen for up to one minute, counting as difficult terrain. A creature attempting to move across this area before it thaws must succeed on a DC 11 Dexterity saving throw or fall prone.")])]),_c('tr',[_c('td',[_vm._v("69-72")]),_c('th',[_vm._v("Fixation")]),_c('td',[_vm._v("Following recovery, you develop an unhealthy fixation. Roll 1d6: on a 1-3, you nurture a bitter enmity; on a 4-6, you develop an overpowering phobia. If you are immune to being frightened, then this result is bitter enmity. You should work with the Dungeon Master on the phrasing of the new flaw: your hatred or phobia may focus on some element of the attacker's identity (for example, a mindless hatred towards dragons or sorcerers) or even the type of attack (such as a fear of snow). This flaw can be removed by magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("73-76")]),_c('th',[_vm._v("Horrible Scar")]),_c('td',[_vm._v(" If you wear a helmet, you must make a DC 14 Dexterity saving throw  if you fail (or have no helmet), you are disfigured to the extent that the wound can't be easily concealed, or palated. You have disadvantage on Charisma (Persuasion) checks and advantage on Charisma (Intimidation) checks. Magic like Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("77-96")]),_c('th',[_vm._v("Minor Scar")]),_c('td',[_vm._v("The scar doesn't have any adverse effect, but may make you more memorable and badass! Magic such as Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("97-98")]),_c('th',[_vm._v("Cold Heart")]),_c('td',[_vm._v("By some freak coincidence, the intensity of the frost opens the briefest of rifts to the Plane of Elemental Water and you are permanently infused with the smallest fragment of energy from that plane. Your body becomes icy cold to the touch and, from now on, you reduce fire damage that you suffer by -3 per attack, and your own unarmed attacks inflict an additional 1 point of magical cold damage. Additionally, once per long rest, upon striking a foe within 10 feet, you can choose to channel an additional 2d6 magical cold damage against the target. This costs no action. This flaw can only be removed by magic such as Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("99+")]),_c('th',[_vm._v("I'm... I'm Alright!")]),_c('td',[_vm._v("The gods must be smiling today  the blow seemed worse than it actually is, and you are assumed dead. Rather than making any death saving throws, you are instead stabilised and returned to 1 hit point and, at the beginning of your next turn, regain 1d10 hit points and can act as normal, gaining advantage on your first attack that turn.")])])])])]),_c('b-tab',{attrs:{"index":"4","title":"Fire"}},[_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("D100 Roll")]),_c('th',[_vm._v("Injury")]),_c('th',[_vm._v("Description")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("1")]),_c('th',[_vm._v("Disintigrated")]),_c('td',[_vm._v("The conflagration burns unnaturally hot, killing you instantly and leaving nothing but ash. You can now only be brought back by powerful magic like True Resurrection or Wish.")])]),_c('tr',[_c('td',[_vm._v("2")]),_c('th',[_vm._v("Instant Death.")]),_c('td',[_vm._v("Your major organs are incinerated, or your brain boils inside your skull, killing you instantly.")])]),_c('tr',[_c('td',[_vm._v("3")]),_c('th',[_vm._v("Fatal Wound")]),_c('td',[_vm._v("You suffer massive burns, or internal burn damage to major organs. Even if you succeed at the normal death saving throws, you remain unconscious and will die after 1d6 turns have passed, at the end of your turn, unless you are magically healed above 0 hit points, or stabilised with a DC 17 Wisdom (Medicine) check using a healer's kit.")])]),_c('tr',[_c('td',[_vm._v("4")]),_c('th',[_vm._v("Incinerated Arm")]),_c('td',[_vm._v("Your arm or hand is almost entirely disintegrated. Due to system shock, you must make a death saving throw immediately, with disadvantage on the roll. Following recovery, you can no longer hold anything with two hands, and can hold only a single object at a time. The distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. Finally, if the lost arm was your dominant arm (50% chance), unless you are trained in the Dual Wielder feat or the Two-Weapon Fighting Style, you suffer disadvantage with weapon attacks until you learn to use your off-hand. You learn to live with this pain and train your off-hand after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("5")]),_c('th',[_vm._v("Incinerated Leg")]),_c('td',[_vm._v("Your leg or foot is almost entirely disintegrated. Due to system shock, you must make a death saving throw immediately, with disadvantage on the roll. Following recovery, your speed on foot is halved and you must use a cane or crutch to move, unless you receive a prosthesis of some kind. Additionally, you fall prone after using the dash action, and suffer disadvantage on Dexterity checks made to balance. Finally, the distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. You learn to live with this pain after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("6-11")]),_c('th',[_vm._v("Multiple Injuries")]),_c('td',[_vm._v("Roll again on this table 1d3+1 times, suffering the combined effects of the various results (within reason!). Reroll any duplicates, and also reroll Multiple Injuries, Minor Scar, Full Recovery, \"I'm... I'm Alright!\" and any results which indicate death (like Fatal Wound, Instant Death or worse).")])]),_c('tr',[_c('td',[_vm._v("12-15")]),_c('th',[_vm._v("Lung Trauma")]),_c('td',[_vm._v("Your lungs are scorched or withered, placing you dangerously close to death. While this flaw remains, you can only stabilise naturally after five death saving throws, rather than three. Following recovery, even routine activity leaves you breathless, and you are wracked with coughing fits when the air is thin or smoky. For every four hours spent walking (or eight hours riding), and when reduced to half your maximum hit points or less, you must succeed at a DC 11 Constitution saving throw or suffer one level of exhaustion. Exhaustion levels gained from this flaw are lost during a short or long rest, or can be removed by a character equipped with a herbalism kit with which they are proficient. Finally, this flaw ends if you succeed at a DC 17 Constitution saving throw rolled at the beginning of every month, or if removed by Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("16-19")]),_c('th',[_vm._v("Frail")]),_c('td',[_vm._v("You recover, but your health is never quite the same again. You can barely keep food down, and lose 1 point from your Constitution score. Finally, if you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce the character's hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("20-23")]),_c('th',[_vm._v("Painful Joints")]),_c('td',[_vm._v("You heal, but are never again as sprightly. You lose 1 point from your Dexterity score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("24-27")]),_c('th',[_vm._v("Enfeebled")]),_c('td',[_vm._v("You heal, but are never again as strong. You lose 1 point from your Strength score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("28-31")]),_c('th',[_vm._v("Leg Wound")]),_c('td',[_vm._v("Your leg is badly damaged. Until a base recovery time of 40 days has passed (see Recovery above), you must use a cane or crutch to move at half speed, you fall prone after using the dash action, and you suffer disadvantage on Dexterity checks made to balance. Even after the worst of the damage has passed, your speed on foot is reduced by 5 feet. The limb can be restored by either Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("32-36")]),_c('th',[_vm._v("Hand Injury")]),_c('td',[_vm._v("The attack costs you several fingers. There is a 50% chance of the affected hand being your dominant hand. You can just about carry a shield, but if it's a weapon hand, your weapon attacks suffer a -2 penalty, unless you are trained in either the Dual Wielder feat or the Two-Weapon Fighting Style. Also, if it is your dominant hand, if you attempt to cast a spell with somatic components, you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. These penalties last a base recovery time of 40 days (see Recovery above). This flaw can be removed by magic such as Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("37-40")]),_c('th',[_vm._v("Troubled Sleep")]),_c('td',[_vm._v("Unless you are immune to being frightened, you suffer the following flaw: though you more or less recover, your sleep or trance is ravaged by inescapable nightmares, and rarely affords much rest. You must succeed on a DC 11 Charisma or Wisdom saving throw (your choice) after every long rest, or only recover a quarter of your hit dice. If you beat the save DC by 10 or more, the flaw ends. It can also be removed by magic such as Dream, Greater Restoration, Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("41-45")]),_c('th',[_vm._v("Ruined Armor")]),_c('td',[_vm._v("Unless your armour is enchanted or made of adamantine, the attack leaves it badly damaged, affording little protection and fitting badly. You suffer -2 from your AC. This penalty lasts until it is replaced, repaired (costing half the the total cost of the item) or subject to a Mending spell. If you have no armour, treat this result as Minor Scar instead.")])]),_c('tr',[_c('td',[_vm._v("46-52")]),_c('th',[_vm._v("Useless Shield")]),_c('td',[_vm._v("Unless your shield is enchanted or made of adamantine, the attack leaves it ruined and worthless. If you don't have a shield, treat this result as Ruined Armour instead.")])]),_c('tr',[_c('td',[_vm._v("53-57")]),_c('th',[_vm._v("Infected Wounds")]),_c('td',[_vm._v("The damage refuses to properly heal, and instead becomes swollen and leaks a noisome pus. Your hit point maximum is reduced by 1 for every 24 hours the wound persists. If your hit point maximum drops to 0, you die. The wound heals if you receive magical healing or, alternatively, someone can tend to the wound and make a DC 14 Wisdom (Medicine) check once every 24 hours. After ten successes, the wound heals. Characters who are immune to disease treat this result as Minor Scar instead.")])]),_c('tr',[_c('td',[_vm._v("58-61")]),_c('th',[_vm._v("Personality Change")]),_c('td',[_vm._v("In the aftermath of the attack, due to post-traumatic stress, you soon exhibit a shift in personality. Roll 1d100 and consult the Indefinite Madness table (on page 260 of the Dungeon Master's Guide) to see what permanent flaw you gain. This flaw can only be removed by magic such as Greater Restoration or Wish, or possibly by some kind of long-term therapy, at the Dungeon Master's discretion.")])]),_c('tr',[_c('td',[_vm._v("62-65")]),_c('th',[_vm._v("Aaaaaaarghhhhh!")]),_c('td',[_vm._v("Unless you are immune to being frightened, you panic as the flames consume you, slamming bodily into a creature that interrupts your maddened flight. You immediately move up to half your speed (rounded up) towards a random creature and attempt a shove (either to knock prone or push, your choice); this movement can result in opportunity attacks. If the shove succeeds, you also inflict 1d6 fire damage. Following this attack, you then fall unconscious, and begin making death saving throws as usual.")])]),_c('tr',[_c('td',[_vm._v("66-70")]),_c('th',[_vm._v("Fixation")]),_c('td',[_vm._v("Following recovery, you develop an unhealthy fixation. Roll 1d6: on a 1-3, you nurture a bitter enmity; on a 4-6, you develop an overpowering phobia. If you are immune to being frightened, then this result is bitter enmity. You should work with the Dungeon Master on the phrasing of the new flaw: your hatred or phobia may focus on some element of the attacker's identity (for example, a mindless hatred towards dragons or sorcerers) or even the type of attack (such as a fear of fire). This flaw can be removed by magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("71-76")]),_c('th',[_vm._v("Horrible Scar")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 14 Dexterity saving throw  if you fail (or have no helmet), you are disfigured to the extent that the wound can't be easily concealed, or palated. You have disadvantage on Charisma (Persuasion) checks and advantage on Charisma (Intimidation) checks. Magic like Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("77-96")]),_c('th',[_vm._v("Minor Scar")]),_c('td',[_vm._v("The scar doesn't have any adverse effect, but may make you more memorable and badass! Magic such as Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("97-98")]),_c('th',[_vm._v("Fire Touched")]),_c('td',[_vm._v("By some freak coincidence, the intensity of the flame opens the briefest of rifts to the Plane of Elemental Fire and, you become permanently infused with the smallest fragment of energy from that plane. Your body radiates heat and, from now on, you reduce cold damage that you suffer by -3 per attack, and your own unarmed attacks inflict an additional 1 point of magical fire damage. Additionally, once per long rest, upon striking a foe within 10 feet, you can choose to channel an additional 2d6 magical fire damage against the target. This costs no action, but if a nonmagical wooden weapon is used to channel this flame, it is destroyed. This flaw can only be removed by magic such as Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("99+")]),_c('th',[_vm._v("I'm... I'm Alright!")]),_c('td',[_vm._v("The gods must be smiling today  the blow seemed worse than it actually is, and you are assumed dead. Rather than making any death saving throws, you are instead stabilised and returned to 1 hit point and, at the beginning of your next turn, regain 1d10 hit points and can act as normal, gaining advantage on your first attack that turn.")])])])])]),_c('b-tab',{attrs:{"index":"5","title":"Force"}},[_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("D100 Roll")]),_c('th',[_vm._v("Injury")]),_c('th',[_vm._v("Description")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("1")]),_c('th',[_vm._v("Hole in Time and Space.")]),_c('td',[_vm._v("Uh oh. The energy of the rogue magic contracts into a super-dense point at your heart and begins to drag all ambient magic inwards  seconds later, your remains crumple silently inward, and all that remains is an orb of impenetrable dark force  a sphere of annihilation! You die instantly and your remains and equipment are destroyed (with the possible exception of any artifact in your possession) and, at the beginning of what would be your next turn, a sphere of annihilation occupies your former space. You can now only be brought back by a Wish.")])]),_c('tr',[_c('td',[_vm._v("2")]),_c('th',[_vm._v("Instant Death.")]),_c('td',[_vm._v("Your skull is crushed, or you suffer massive internal injuries, killing you instantly.")])]),_c('tr',[_c('td',[_vm._v("3")]),_c('th',[_vm._v("Fatal Wound")]),_c('td',[_vm._v("Either your back is broken, or your internal organs are ruptured. Even if you succeed at the normal death saving throws, you remain unconscious and will die after 1d6 turns have passed, at the end of your turn, unless you are magically healed above 0 hit points, or stabilised with a DC 17 Wisdom (Medicine) check using a healer's kit.")])]),_c('tr',[_c('td',[_vm._v("4")]),_c('th',[_vm._v("Paralysis")]),_c('td',[_vm._v("Spinal damage leaves you unable to move or feel anything from the waist down. You will need carrying or some sort of device to allow mobility (otherwise you can only crawl 5 feet per turn), and count as restrained. Still, there is always hope for a particularly strong person: at the beginning of every week, you can make a DC 20 Constitution saving throw  on a success, you miraculously recover. Otherwise, this flaw only ends if you receive magic such as Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("5")]),_c('th',[_vm._v("Shattered Arm")]),_c('td',[_vm._v("Your arm or hand is smashed beyond recovery, and will require amputation. Following recovery, you can no longer hold anything with two hands, and can hold only a single object at a time. The distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. Finally, if the lost arm was your dominant arm (50% chance), unless you are trained in the Dual Wielder feat or the Two-Weapon Fighting Style, you suffer disadvantage with weapon attacks until you learn to use your off-hand. You learn to live with this pain and train your off-hand after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("6")]),_c('th',[_vm._v("Shattered Leg")]),_c('td',[_vm._v("Your leg or foot is smashed beyond recovery, and will require amputation. Following recovery, your speed on foot is halved and you must use a cane or crutch to move, unless you receive a prosthesis of some kind. Additionally, you fall prone after using the dash action, and suffer disadvantage on Dexterity checks made to balance. Finally, the distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. You learn to live with this pain after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("7-11")]),_c('th',[_vm._v("Multiple Injuries")]),_c('td',[_vm._v("Roll again on this table 1d3+1 times, suffering the combined effects of the various results (within reason!). Reroll any duplicates, and also reroll Multiple Injuries, Minor Scar, Full Recovery, \"I'm... I'm Alright!\" and any results which indicate death (like Fatal Wound, Instant Death or worse).")])]),_c('tr',[_c('td',[_vm._v("12-15")]),_c('th',[_vm._v("Internal Bleeding.")]),_c('td',[_vm._v("The attack leaves you with hidden damage, drawing you towards death. You lose 1d3 hit points at the end of every hour, even if stabilised. This injury is difficult to spot, but once diagnosed, This flaw can be removed by magic such as Heal, Regenerate or Wish, or if another character proficient in Medicine conducts surgery: this takes 1 hour, expends one use of a healer's kit and requires a successful DC 17 Wisdom (Medicine) check. The surgery itself inflicts 3d6 piercing damage.")])]),_c('tr',[_c('td',[_vm._v("16-19")]),_c('th',[_vm._v("Frail")]),_c('td',[_vm._v("You recover, but your health is never quite the same again. You can barely keep food down, and lose 1 point from your Constitution score. Finally, if you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce the character's hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("20-23")]),_c('th',[_vm._v("Painful Joints")]),_c('td',[_vm._v("You heal, but are never again as sprightly. You lose 1 point from your Dexterity score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("24-29")]),_c('th',[_vm._v("Enfeebled")]),_c('td',[_vm._v("You heal, but are never again as strong. You lose 1 point from your Strength score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("30-33")]),_c('th',[_vm._v("Head Injury")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 11 Constitution saving throw  if you fail (or have no helmet), ten head trauma damages your mental facilities somehow, often leaving a bald patch at the scar. Roll 1d3: 1: you lose 1 point from your Intelligence score; 2: you lose 1 point from your Wisdom score; 3: you lose 1 point from your Charisma score. This flaw is often accompanied by speech or memory problems, requiring you to relearn basic skills and social niceties. If the same ability score is affected a second time or more in the future by this flaw, you must succeed at a DC 14 Constitution saving throw or also suffer the Coma flaw. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("34-38")]),_c('th',[_vm._v("Broken Arm")]),_c('td',[_vm._v("As the attack slams home, an audible 'crack' renders an arm close to useless for a time. There is a 50% chance that the broken arm is your dominant arm and if so, unless you are trained in the Dual Wielder feat or the Two-Weapon Fighting Style, you suffer disadvantage with weapon attacks. If it is your other arm, you cannot wear a shield. This flaw ends after a base recovery time of 30 weeks (see Recovery above), or if you receive magic such as Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("39-43")]),_c('th',[_vm._v("Deafened")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 14 Constitution saving throw  if you fail (or have no helmet), then either through brain damage or damage to the ears, the attack leaves you deafened. This flaw is removed if you receive magic such as Greater Restoration, Heal, Lesser Restoration, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("44-47")]),_c('th',[_vm._v("Leg Wound")]),_c('td',[_vm._v("Your leg is badly damaged. Until a base recovery time of 40 days has passed (see Recovery above), you must use a cane or crutch to move at half speed, you fall prone after using the dash action, and you suffer disadvantage on Dexterity checks made to balance. Even after the worst of the damage has passed, your speed on foot is reduced by 5 feet. The limb can be restored by either Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("48-52")]),_c('th',[_vm._v("Broken Ribs")]),_c('td',[_vm._v("Your rib cage is partly caved in. Whenever you attempt a Strength (Athletics) check or attempt to take any action in combat (other than casting a spell), you must succeed at a DC 11 Constitution saving throw. If you fail the save, you lose your action and can't use reactions until the start of your next turn. This flaw ends after a base recovery time of 50 days (see Recovery above), or if you receive magic such as Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("53-57")]),_c('th',[_vm._v("Punch Drunk")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 11 Constitution saving throw  if you fail (or have no helmet), then due to hearing impairment or brain trauma, your coordination is often disrupted. After every short or long rest, you must succeed at a DC 11 Constitution or Wisdom saving throw (your choice), or suffer disadvantage on Dexterity (Acrobatics) checks. This penalty lasts until the next short or long rest. Additionally, if you are knocked prone, it costs you double the standard movement to stand up. This flaw can be removed through magic such as Greater Restoration, Heal, Lesser Restoration, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("58-62")]),_c('th',[_vm._v("Shell-Shock")]),_c('td',[_vm._v("Either due to brain damage or a lifetime of facing horrors and death, you are somewhat indecisive and slow to react. You suffer a penalty of -2 to your Initiative. This flaw can be removed through magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("63-66")]),_c('th',[_vm._v("Ruined Armor")]),_c('td',[_vm._v("Unless your armour is enchanted or made of adamantine, the attack leaves it badly damaged, affording little protection and fitting badly. You suffer -2 from your AC. This penalty lasts until it is replaced, repaired (costing half the the total cost of the item) or subject to a Mending spell. If you have no armour, treat this result as Minor Scar instead.")])]),_c('tr',[_c('td',[_vm._v("67-68")]),_c('th',[_vm._v("Force Magnet")]),_c('td',[_vm._v("Somehow, the magic of the attack embeds itself into your being and begins to attract other force energy towards you. From now on, whenever magic missile is cast by any spellcaster (including you!) and you are within its range, an extra missile flies off and strikes you, for normal damage. This flaw can only be removed by magic such as Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("69-72")]),_c('th',[_vm._v("Personality Change")]),_c('td',[_vm._v("In the aftermath of the attack, either as a result of head injury or due to post-traumatic stress, you soon exhibit a shift in personality. Roll 1d100 and consult the Indefinite Madness table (on page 260 of the Dungeon Master's Guide) to see what permanent flaw you gain. This flaw can only be removed by magic such as Greater Restoration or Wish, or possibly by some kind of long-term therapy, at the Dungeon Master's discretion.")])]),_c('tr',[_c('td',[_vm._v("73-76")]),_c('th',[_vm._v("Fastball Special")]),_c('td',[_vm._v("The force of the attack sends you haplessly flying through the air towards another unsuspecting creature. You are pushed up to 10 feet towards a random creature and, if in range to strike it, knock it prone, unless that other creature succeeds at a DC 11 Dexterity or Strength saving throw (creature's choice). This roll is made with disadvantage if you are bigger than the dodging creature. If you are huge or gargantuan, you are too heavy to be thrown, and this result has no effect.")])]),_c('tr',[_c('td',[_vm._v("77-96")]),_c('th',[_vm._v("Full Recovery")]),_c('td',[_vm._v("You seem to be fine.")])]),_c('tr',[_c('td',[_vm._v("97-98")]),_c('th',[_vm._v("One with the Magic")]),_c('td',[_vm._v("Although the spell takes effect as normal, the mystic energy refuses to ebb away but instead weaves around and into you. Following recovery, you soon learn a couple of ways of harnessing this magic for simple telekinetic effects. Once per long rest, you can cast either mage hand or shield. These function as the standard spell (including being cancelled by Antimagic Field, Counterspell, Dispel Magic and the like), but don't use up any spell slot. This flaw can only be removed by magic such as Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("99+")]),_c('th',[_vm._v("I'm... I'm Alright!")]),_c('td',[_vm._v("The gods must be smiling today  the blow seemed worse than it actually is, and you are assumed dead. Rather than making any death saving throws, you are instead stabilised and returned to 1 hit point and, at the beginning of your next turn, regain 1d10 hit points and can act as normal, gaining advantage on your first attack that turn.")])])])])]),_c('b-tab',{attrs:{"index":"6","title":"Lightning"}},[_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("D100 Roll")]),_c('th',[_vm._v("Injury")]),_c('th',[_vm._v("Description")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("1")]),_c('th',[_vm._v("Disintigrated.")]),_c('td',[_vm._v("The conflagration burns unnaturally hot, killing you instantly and leaving nothing but ash. You can now only be brought back by powerful magic like True Resurrection or Wish.")])]),_c('tr',[_c('td',[_vm._v("2")]),_c('th',[_vm._v("Instant Death.")]),_c('td',[_vm._v("You suffer a massive overload of your nervous system, or your brain is shorted out, killing you instantly.")])]),_c('tr',[_c('td',[_vm._v("3")]),_c('th',[_vm._v("Fatal Wound")]),_c('td',[_vm._v("You suffer massive burns, or internal burn damage to major organs. Even if you succeed at the normal death saving throws, you remain unconscious and will die after 1d6 turns have passed, at the end of your turn, unless you are magically healed above 0 hit points, or stabilised with a DC 17 Wisdom (Medicine) check using a healer's kit.")])]),_c('tr',[_c('td',[_vm._v("4")]),_c('th',[_vm._v("Paralysis")]),_c('td',[_vm._v("Spinal damage leaves you unable to move or feel anything from the waist down. You will need carrying or some sort of device to allow mobility (otherwise you can only crawl 5 feet per turn), and count as restrained. Still, there is always hope for a particularly strong person: at the beginning of every week, you can make a DC 20 Constitution saving throw  on a success, you miraculously recover. Otherwise, this flaw only ends if you receive magic such as Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("5")]),_c('th',[_vm._v("Incinerated Arm")]),_c('td',[_vm._v("Your arm or hand is almost entirely disintegrated. Due to system shock, you must make a death saving throw immediately, with disadvantage on the roll. Following recovery, you can no longer hold anything with two hands, and can hold only a single object at a time. The distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. Finally, if the lost arm was your dominant arm (50% chance), unless you are trained in the Dual Wielder feat or the Two-Weapon Fighting Style, you suffer disadvantage with weapon attacks until you learn to use your off-hand. You learn to live with this pain and train your off-hand after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("6")]),_c('th',[_vm._v("Incinerated Leg")]),_c('td',[_vm._v("Your leg or foot is almost entirely disintegrated. Due to system shock, you must make a death saving throw immediately, with disadvantage on the roll. Following recovery, your speed on foot is halved and you must use a cane or crutch to move, unless you receive a prosthesis of some kind. Additionally, you fall prone after using the dash action, and suffer disadvantage on Dexterity checks made to balance. Finally, the distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. You learn to live with this pain after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("7-11")]),_c('th',[_vm._v("Multiple Injuries")]),_c('td',[_vm._v("Roll again on this table 1d3+1 times, suffering the combined effects of the various results (within reason!). Reroll any duplicates, and also reroll Multiple Injuries, Minor Scar, Full Recovery, \"I'm... I'm Alright!\" and any results which indicate death (like Fatal Wound, Instant Death or worse).")])]),_c('tr',[_c('td',[_vm._v("12-15")]),_c('th',[_vm._v("Lung Trauma")]),_c('td',[_vm._v("Your lungs are scorched or withered, placing you dangerously close to death. While this flaw remains, you can only stabilise naturally after five death saving throws, rather than three. Following recovery, even routine activity leaves you breathless, and you are wracked with coughing fits when the air is thin or smoky. For every four hours spent walking (or eight hours riding), and when reduced to half your maximum hit points or less, you must succeed at a DC 11 Constitution saving throw or suffer one level of exhaustion. Exhaustion levels gained from this flaw are lost during a short or long rest, or can be removed by a character equipped with a herbalism kit with which they are proficient. Finally, this flaw ends if you succeed at a DC 17 Constitution saving throw rolled at the beginning of every month, or if removed by Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("16-19")]),_c('th',[_vm._v("Frail")]),_c('td',[_vm._v("You recover, but your health is never quite the same again. You can barely keep food down, and lose 1 point from your Constitution score. Finally, if you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce the character's hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("20-23")]),_c('th',[_vm._v("Painful Joints")]),_c('td',[_vm._v("You heal, but are never again as sprightly. You lose 1 point from your Dexterity score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("24-26")]),_c('th',[_vm._v("Enfeebled")]),_c('td',[_vm._v("You heal, but are never again as strong. You lose 1 point from your Strength score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("27-30")]),_c('th',[_vm._v("Brain Damage")]),_c('td',[_vm._v("The attack damages your mental facilities somehow. Roll 1d3: 1: you lose 1 point from your Intelligence score; 2: you lose 1 point from your Wisdom score; 3: you lose 1 point from your Charisma score. This flaw is often accompanied by speech or memory problems, requiring you to relearn basic skills and social niceties. If the same ability score is affected a second time or more in the future by this flaw, you must succeed at a DC 14 Constitution saving throw or also suffer the Coma flaw. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("31-35")]),_c('th',[_vm._v("Blasted Eye")]),_c('td',[_vm._v("One of your eyes is destroyed by the burst of electricity. From now on, you suffer disadvantage on Wisdom (Perception) checks that rely on sight, and on ranged attack rolls. If a subsequent injury damages your other eye, you are permanently blinded. Magic such as Regenerate or Wish can restore the lost eye.")])]),_c('tr',[_c('td',[_vm._v("36-40")]),_c('th',[_vm._v("Deafened")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 14 Constitution saving throw  if you fail (or have no helmet), then either through brain damage or damage to the ears, the attack leaves you deafened. This flaw is removed if you receive magic such as Greater Restoration, Heal, Lesser Restoration, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("41-45")]),_c('th',[_vm._v("Pelvic Injury")]),_c('td',[_vm._v("Either your pelvis is struck a crippling blow, or the attack destroys your nethers. Whenever yo attempt a Strength (Athletics) check or take any action in combat (other than casting a spell), you must succeed at a DC 11 Constitution saving throw or lose 1d6 hit points. You also can't ride a horse without suffering 1d10 hit points. Finally, this injury renders you permanently infertile. This flaw ends after a base recovery time of 70 days (see Recovery above), or if you receive magic such as Heal, Regenerate or Wish, although the infertility can only be reversed by Greater Restoration or Remove Curse.")])]),_c('tr',[_c('td',[_vm._v("46-51")]),_c('th',[_vm._v("Punch Drunk")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 11 Constitution saving throw  if you fail (or have no helmet), then due to hearing impairment or brain trauma, your coordination is often disrupted. After every short or long rest, you must succeed at a DC 11 Constitution or Wisdom saving throw (your choice), or suffer disadvantage on Dexterity (Acrobatics) checks. This penalty lasts until the next short or long rest. Additionally, if you are knocked prone, it costs you double the standard movement to stand up. This flaw can be removed through magic such as Greater Restoration, Heal, Lesser Restoration, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("52-57")]),_c('th',[_vm._v("Shell-Shock")]),_c('td',[_vm._v("Either due to brain damage or a lifetime of facing horrors and death, you are somewhat indecisive and slow to react. You suffer a penalty of -2 to your Initiative. This flaw can be removed through magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("58-63")]),_c('th',[_vm._v("Ruined Armor")]),_c('td',[_vm._v("Unless your armour is enchanted or made of adamantine, the attack leaves it badly damaged, affording little protection and fitting badly. You suffer -2 from your AC. This penalty lasts until it is replaced, repaired (costing half the the total cost of the item) or subject to a Mending spell. If you have no armour, treat this result as Minor Scar instead.")])]),_c('tr',[_c('td',[_vm._v("64-67")]),_c('th',[_vm._v("Chain Discharge")]),_c('td',[_vm._v("The electricity crackles over you for a moment and then, as you silently collapse, arcs out towards others nearby. All other creatures within 10-foot range of you must succeed on a DC 11 Dexterity saving throw or suffer 2d6 lightning damage.")])]),_c('tr',[_c('td',[_vm._v("69-72")]),_c('th',[_vm._v("Fixation")]),_c('td',[_vm._v("Following recovery, you develop an unhealthy fixation. Roll 1d6: on a 1-3, you nurture a bitter enmity; on a 4-6, you develop an overpowering phobia. If you are immune to being frightened, then this result is bitter enmity. You should work with the Dungeon Master on the phrasing of the new flaw: your hatred or phobia may focus on some element of the attacker's identity (for example, a mindless hatred towards dragons or sorcerers) or even the type of attack (such as a fear of storms). This flaw can be removed by magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("73-76")]),_c('th',[_vm._v("Hideous Scar")]),_c('td',[_vm._v("You are scorched to the extent that the wound can't be easily concealed, or palated. You have disadvantage on Charisma (Persuasion) checks and advantage on Charisma (Intimidation) checks. Magic such as Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("77-96")]),_c('th',[_vm._v("Minor Scar")]),_c('td',[_vm._v("The scar doesn't have any adverse effect, but may make you more memorable and badass! Magic such as Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("97-98")]),_c('th',[_vm._v("Storm Blood")]),_c('td',[_vm._v("By some freak coincidence, the intensity of the electricity opens the briefest of rifts to the Plane of Elemental Air and you become permanently infused with the smallest fragment of energy from that plane. Your body flickers with static, and from now on anyone who hits you in melee must succeed at a DC 14 Dexterity saving throw or suffer 1d6 magical lightning damage, and your unarmed attacks inflict an additional 1 point of magical lightning damage. This costs no action. This flaw can only be removed by magic such as Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("99+")]),_c('th',[_vm._v("I'm... I'm Alright!")]),_c('td',[_vm._v("The gods must be smiling today  the blow seemed worse than it actually is, and you are assumed dead. Rather than making any death saving throws, you are instead stabilised and returned to 1 hit point and, at the beginning of your next turn, regain 1d10 hit points and can act as normal, gaining advantage on your first attack that turn.")])])])])]),_c('b-tab',{attrs:{"index":"7","title":"Necrotic"}},[_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("D100 Roll")]),_c('th',[_vm._v("Injury")]),_c('th',[_vm._v("Description")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("1")]),_c('th',[_vm._v("Hole in Time and Space.")]),_c('td',[_vm._v("Uh oh. The energy of the rogue magic contracts into a super-dense point at your heart and begins to drags all ambient magic inwards  seconds later, your remains crumple silently inward, and all that remains is an orb of impenetrable dark force  a sphere of annihilation! You die instantly and your remains and equipment are destroyed (with the possible exception of any artifact in your possession) and, at the beginning of what would be your next turn, a sphere of annihilation occupies your former space. You can now only be brought back by a Wish.")])]),_c('tr',[_c('td',[_vm._v("2")]),_c('th',[_vm._v("Restless Death")]),_c('td',[_vm._v("Your life is snuffed out, and you don't even get the relief of the afterlife. You die instantly, and at the beginning of what would be your next turn (if you have not been magically revived somehow), a spectre rises in your space. You can now only be brought back by powerful magic like Reincarnate, True Resurrection or Wish.")])]),_c('tr',[_c('td',[_vm._v("3")]),_c('th',[_vm._v("Instant Death")]),_c('td',[_vm._v("The dark energy withers you utterly, leaving only a mummified husk  you are killed instantly.")])]),_c('tr',[_c('td',[_vm._v("4")]),_c('th',[_vm._v("Fatal Wound")]),_c('td',[_vm._v("Either you suffer massive tissue damage, or major organs are part eaten away. Even if you succeed at the normal death saving throws, you remain unconscious and will die after 1d6 turns have passed, at the end of your turn, unless you are magically healed above 0 hit points, or stabilised with a DC 17 Wisdom (Medicine) check using a healer's kit.")])]),_c('tr',[_c('td',[_vm._v("5")]),_c('th',[_vm._v("Coma")]),_c('td',[_vm._v("You enter a death-like sleep from which nothing can seemingly awaken you, though dim life signs still remain. You remain unconscious, even after being stabilised. Following a long rest, make a death saving throw. Three failed saves result in death (possibly due to lack of fluids, amongst other complications), but a result of 15 or means the coma ends. If and when you awaken, you suffer three levels of exhaustion (these exhaustion levels can be removed normally). The coma can also be ended prematurely by magic such as Dream, Greater Restoration, Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("6")]),_c('th',[_vm._v("Shrivelled Arm")]),_c('td',[_vm._v("Your arm or hand is withered beyond recovery, and will require amputation. Following recovery, you can no longer hold anything with two hands, and can hold only a single object at a time. The distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. Finally, if the lost arm was your dominant arm (50% chance), unless you are trained in the Dual Wielder feat or the Two-Weapon Fighting Style, you suffer disadvantage with weapon attacks until you learn to use your off-hand. You learn to live with this pain and train your off-hand after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("7")]),_c('th',[_vm._v("Shrivelled Leg")]),_c('td',[_vm._v("Your leg or foot is withered beyond recovery, and will require amputation. Following recovery, your speed on foot is halved and you must use a cane or crutch to move, unless you receive a prosthesis of some kind. Additionally, you fall prone after using the dash action, and suffer disadvantage on Dexterity checks made to balance. Finally, the distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. You learn to live with this pain after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("8-11")]),_c('th',[_vm._v("Multiple Injuries")]),_c('td',[_vm._v("Roll again on this table 1d3+1 times, suffering the combined effects of the various results (within reason!). Reroll any duplicates, and also reroll Multiple Injuries, Minor Scar, Full Recovery, \"I'm... I'm Alright!\" and any results which indicate death (like Fatal Wound, Instant Death or worse).")])]),_c('tr',[_c('td',[_vm._v("12-15")]),_c('th',[_vm._v("Lung Trauma")]),_c('td',[_vm._v("Your lungs are scorched or withered, placing you dangerously close to death. While this flaw remains, you can only stabilise naturally after five death saving throws, rather than three. Following recovery, even routine activity leaves you breathless, and you are wracked with coughing fits when the air is thin or smoky. For every four hours spent walking (or eight hours riding), and when reduced to half your maximum hit points or less, you must succeed at a DC 11 Constitution saving throw or suffer one level of exhaustion. Exhaustion levels gained from this flaw are lost during a short or long rest, or can be removed by a character equipped with a herbalism kit with which they are proficient. Finally, this flaw ends if you succeed at a DC 17 Constitution saving throw rolled at the beginning of every month, or if removed by Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("16-19")]),_c('th',[_vm._v("Frail")]),_c('td',[_vm._v("You recover, but your health is never quite the same again. You can barely keep food down, and lose 1 point from your Constitution score. Finally, if you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce the character's hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("20-23")]),_c('th',[_vm._v("Painful Joints")]),_c('td',[_vm._v("You heal, but are never again as sprightly. You lose 1 point from your Dexterity score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("24-27")]),_c('th',[_vm._v("Enfeebled")]),_c('td',[_vm._v("You heal, but are never again as strong. You lose 1 point from your Strength score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("28-31")]),_c('th',[_vm._v("Ruined Eye")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 17 Dexterity saving throw  if you fail (or have no helmet), one of your eyes is destroyed. From now on, you suffer disadvantage on Wisdom (Perception) checks that rely on sight, and on ranged attack rolls. If a subsequent injury damages your other eye, you are permanently blinded. Magic such as Regenerate or Wish can restore the lost eye.")])]),_c('tr',[_c('td',[_vm._v("32-36")]),_c('th',[_vm._v("Pelvic Injury")]),_c('td',[_vm._v("Either your pelvis is struck a crippling blow, or the attack destroys your nethers. Whenever you attempt a Strength (Athletics) check or take any action in combat (other than casting a spell), you must succeed at a DC 11 Constitution saving throw or lose 1d6 hit points. You also can't ride a horse without suffering 1d10 hit points. Finally, this injury renders you permanently infertile. This flaw ends after a base recovery time of 70 days (see Recovery above), or if you receive magic such as Heal, Regenerate or Wish, although the infertility can only be removed by Greater Restoration or Remove Curse.")])]),_c('tr',[_c('td',[_vm._v("37-40")]),_c('th',[_vm._v("Agonizing Wound")]),_c('td',[_vm._v("The injury plagues you with flashes of searing pain from time to time. Whenever you attempt a Strength (Athletics) check or attempt to take any action in combat (other than casting a spell), you must succeed at a DC 11 Constitution saving throw. If you fail the save, you lose your action and can't use reactions until the start of your next turn. This flaw ends after a base recovery time of 70 days (see Recovery above), and is also removed by magic such as Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("41-46")]),_c('th',[_vm._v("Crippling Fatigue")]),_c('td',[_vm._v("You can never shake your sense of tiredness, and gain one level of exhaustion. This can be removed normally. However, following every long rest, you must succeed at a DC 11 Constitution or Wisdom saving throw (your choice), or gain a level of exhaustion. If you fail this saving throw by 5 or more, you gain two levels. Accumulating enough exhaustion levels can lead to your death. However, if you roll an unmodified 20, this flaw ends, and can also be removed by magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("47-53")]),_c('th',[_vm._v("Stolen Life")]),_c('td',[_vm._v("Unless you are immune to magical ageing, you physically age by 1d6 years, and are rendered infertile. For every year you age, you suffer an additional 1d4 necrotic damage: this damage may lead to instant death. If this flaw is inflicted by a creature (or a spell cast by one), then that creature gains temporary hit points equal to the additional necrotic damage inflicted. The infertility can only be reversed by magic such as either Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("54-58")]),_c('th',[_vm._v("Troubled Sleep")]),_c('td',[_vm._v("Unless you are immune to being frightened, you suffer the following flaw: though you more or less recover, your sleep or trance is ravaged by inescapable nightmares, and rarely affords much rest. You must succeed on a DC 11 Charisma or Wisdom saving throw (your choice) after every long rest, or only recover a quarter of your hit dice. If you beat the save DC by 10 or more, the flaw ends. It can also be removed by magic such as Dream, Greater Restoration, Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("59-65")]),_c('th',[_vm._v("Infected Wounds")]),_c('td',[_vm._v("The damage refuses to properly heal, and instead becomes swollen and leaks a noisome pus. Your hit point maximum is reduced by 1 for every 24 hours the wound persists. If your hit point maximum drops to 0, you die. The wound heals if you receive magical healing or, alternatively, someone can tend to the wound and make a DC 14 Wisdom (Medicine) check once every 24 hours. After ten successes, the wound heals. Characters who are immune to disease treat this result as Minor Scar instead.")])]),_c('tr',[_c('td',[_vm._v("66-69")]),_c('th',[_vm._v("Personality Change")]),_c('td',[_vm._v("In the aftermath of the attack, due to post-traumatic stress, you soon exhibit a shift in personality. Roll 1d100 and consult the Indefinite Madness table (on page 260 of the Dungeon Master's Guide) to see what permanent flaw you gain. This flaw can only be removed by magic such as Greater Restoration or Wish, or possibly by some kind of long-term therapy, at the Dungeon Master's discretion.")])]),_c('tr',[_c('td',[_vm._v("70-73")]),_c('th',[_vm._v("Fixation")]),_c('td',[_vm._v("Following recovery, you develop an unhealthy fixation. Roll 1d6: on a 1-3, you nurture a bitter enmity; on a 4-6, you develop an overpowering phobia. If you are immune to being frightened, then this result is bitter enmity. You should work with the Dungeon Master on the phrasing of the new flaw: your hatred or phobia may focus on some element of the attacker's identity (for example, a mindless hatred towards undead or sorcerers) or even the type of attack (such as a fear of germs). This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("74-76")]),_c('th',[_vm._v("Horrible Scar")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 14 Dexterity saving throw  if you fail (or have no helmet), you are disfigured to the extent that the wound can't be easily concealed, or palated. You have disadvantage on Charisma (Persuasion) checks and advantage on Charisma (Intimidation) checks. Magic like Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("77-96")]),_c('th',[_vm._v("Minor Scar")]),_c('td',[_vm._v("The scar doesn't have any adverse effect, but may make you more memorable and badass! Magic such as Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("97-98")]),_c('th',[_vm._v("Dead One")]),_c('td',[_vm._v("The necrotic energy fails to overcome your will to live, but instead somehow alters you into a being not undead, but not entirely living either. Diminutive plants and beasts (with 1 hit point or less) die within 5 feet of you, and the condition of the sick, elderly and young in your presence worsen. People with whom you have regular contact begin to find you 'wrong' in some intangible way. You reduce necrotic damage that you suffer by -3 per attack. Additionally, most undead fail to perceive you as a living being, and unintelligent undead (such as zombies or skeletons) tend to ignore you, unless you take hostile action. This flaw can only be removed by magic such as Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("99+")]),_c('th',[_vm._v("I'm... I'm Alright!")]),_c('td',[_vm._v("The gods must be smiling today  the blow seemed worse than it actually is, and you are assumed dead. Rather than making any death saving throws, you are instead stabilised and returned to 1 hit point and, at the beginning of your next turn, regain 1d10 hit points and can act as normal, gaining advantage on your first attack that turn.")])])])])]),_c('b-tab',{attrs:{"index":"8","title":"Piercing"}},[_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("D100 Roll")]),_c('th',[_vm._v("Injury")]),_c('th',[_vm._v("Description")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("1")]),_c('th',[_vm._v("Instant Death")]),_c('td',[_vm._v("You are pierced through the heart, or suffer some other grievous wound which kills you instantly.")])]),_c('tr',[_c('td',[_vm._v("2")]),_c('th',[_vm._v("Fatal Wound")]),_c('td',[_vm._v("You are are gutted, or stabbed through the lung or throat. Even if you succeed at the normal death saving throws, you remain unconscious and will die after 1d6 turns have passed, at the end of your turn, unless you are magically healed above 0 hit points, or stabilised with a DC 17 Wisdom (Medicine) check using a healer's kit.")])]),_c('tr',[_c('td',[_vm._v("3")]),_c('th',[_vm._v("Severed Arm")]),_c('td',[_vm._v("Your arm or hand is hacked off, pumping arterial blood everywhere. Due to the flow of blood, you lose 1d6 hit points at the end of each of your turns, unless you receive magical healing to seal the stump, it is cauterised by fire or radiant damage, or another character uses an action to apply a tourniquet. Due to system shock, you must make a death saving throw immediately, with disadvantage on the roll. Following recovery, you can no longer hold anything with two hands, and can hold only a single object at a time. The distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. Finally, if the lost arm was your dominant arm (50% chance), unless you are trained in the Dual Wielder feat or the Two-Weapon Fighting Style, you suffer disadvantage with weapon attacks until you learn to use your off-hand. You learn to live with this pain and train your off-hand after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("4")]),_c('th',[_vm._v("Severed Leg")]),_c('td',[_vm._v("Your leg or foot is hacked off, pumping arterial blood everywhere. Due to the flow of blood, you lose 1d6 hit points at the end of each of your turns, unless you receive magical healing to seal the stump, it is cauterised by fire or radiant damage, or another character uses an action to apply a tourniquet. Due to system shock, you must make a death saving throw immediately, with disadvantage on the roll. Following recovery, your speed on foot is halved and you must use a cane or crutch to move, unless you receive a prosthesis of some kind. Additionally, you fall prone after using the dash action, and suffer disadvantage on Dexterity checks made to balance. Finally, the distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. You learn to live with this pain after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("5-11")]),_c('th',[_vm._v("Multiple Injuries")]),_c('td',[_vm._v("Roll again on this table 1d3+1 times, suffering the combined effects of the various results (within reason!). Reroll any duplicates, and also reroll Multiple Injuries, Minor Scar, Full Recovery, \"I'm... I'm Alright!\" and any results which indicate death (like Fatal Wound or Instant Death).")])]),_c('tr',[_c('td',[_vm._v("12-15")]),_c('th',[_vm._v("Punctured Lung")]),_c('td',[_vm._v("Your lungs are pierced and part collapsed, placing you dangerously close to death. While this flaw remains, you can only stabilise naturally after five death saving throws, rather than three. Following recovery, even routine activity leaves you breathless, and you are wracked with coughing fits when the air is thin or smoky. For every four hours spent walking (or eight hours riding), and when reduced to half your maximum hit points or less, you must succeed at a DC 11 Constitution saving throw or suffer one level of exhaustion. Exhaustion levels gained from this flaw are lost during a short or long rest, or can be removed by a character equipped with a herbalism kit with which they are proficient. Finally, this flaw ends if you succeed at a DC 17 Constitution saving throw rolled at the beginning of every month, or if removed by Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("16-19")]),_c('th',[_vm._v("Internal Bleeding")]),_c('td',[_vm._v("The attack leaves you with hidden damage, drawing you towards death. You lose 1d3 hit points at the end of every hour, even if stabilised. This injury is difficult to spot, but once diagnosed, This flaw can be removed by magic such as Heal, Regenerate or Wish, or if another character proficient in Medicine conducts surgery: this takes 1 hour, expends one use of a healer's kit and requires a successful DC 17 Wisdom (Medicine) check. The surgery itself inflicts 3d6 piercing damage.")])]),_c('tr',[_c('td',[_vm._v("20-23")]),_c('th',[_vm._v("Frail")]),_c('td',[_vm._v("You recover, but your health is never quite the same again. You can barely keep food down, and lose 1 point from your Constitution score. Finally, if you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce the character's hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("24-27")]),_c('th',[_vm._v("Painful Joints")]),_c('td',[_vm._v("You heal, but are never again as sprightly. You lose 1 point from your Dexterity score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("28-31")]),_c('th',[_vm._v("Enfeebled")]),_c('td',[_vm._v("You heal, but are never again as strong. You lose 1 point from your Strength score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("32-35")]),_c('th',[_vm._v("Gouged Eye")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 14 Dexterity saving throw  if you fail (or have no helmet), one of your eyes is pierced, either by the foe's weapon or the dent in your helm. From now on, you suffer disadvantage on Wisdom (Perception) checks that rely on sight, and on ranged attack rolls. If a subsequent injury damages your other eye, you are permanently blinded. Magic such as Regenerate or Wish can restore the lost eye.")])]),_c('tr',[_c('td',[_vm._v("36-39")]),_c('th',[_vm._v("Pelvic Injury")]),_c('td',[_vm._v("Either your pelvis is struck a crippling blow, or the attack destroys your nethers. Whenever you attempt a Strength (Athletics) check or take any action in combat (other than casting a spell), you must succeed at a DC 11 Constitution saving throw or lose 1d6 hit points. You also can't ride a horse without suffering 1d10 hit points. Finally, this injury renders you permanently infertile. This flaw ends after a base recovery time of 70 days (see Recovery above), or if you receive magic such as Heal, Regenerate or Wish, although the infertility can only be reversed by Greater Restoration or Remove Curse.")])]),_c('tr',[_c('td',[_vm._v("40-43")]),_c('th',[_vm._v("Agonizing Wound")]),_c('td',[_vm._v("The injury plagues you with flashes of searing pain from time to time. Whenever you attempt a Strength (Athletics) check or attempt to takes any action in combat (other than casting a spell), you must succeed at a DC 11 Constitution saving throw. If you fail the save, you lose your action and can't use reactions until the start of your next turn. This flaw ends after a base recovery time of 70 days (see Recovery above), and is also removed by magic such as Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("44-47")]),_c('th',[_vm._v("Leg Wound")]),_c('td',[_vm._v("Your leg is badly damaged. Until a base recovery time of 40 days has passed (see Recovery above), you must use a cane or crutch to move at half speed, you fall prone after using the dash action, and you suffer disadvantage on Dexterity checks made to balance. Even after the worst of the damage has passed, your speed on foot is reduced by 5 feet. The limb can be restored by either Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("48-51")]),_c('th',[_vm._v("Shell-Shock")]),_c('td',[_vm._v(" Either due to brain damage or a lifetime of facing horrors and death, you are somewhat indecisive and slow to react. You suffer a penalty of -2 to your Initiative. This flaw can be removed through magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("52-55")]),_c('th',[_vm._v("Hand Injury")]),_c('td',[_vm._v("The attack costs you several fingers. There is a 50% chance of the affected hand being your dominant hand. You can just about carry a shield, but if it's a weapon hand, your weapon attacks suffer a -2 penalty, unless you are trained in either the Dual Wielder feat or the Two-Weapon Fighting Style. Also, if it is your dominant hand, if you attempt to cast a spell with somatic components, you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. These penalties last a base recovery time of 40 days (see Recovery above). This flaw can be removed by magic such as Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("56-59")]),_c('th',[_vm._v("Bleeding Wound")]),_c('td',[_vm._v("Frustratingly, the latest injury is loathe to stop bleeding. When you roll for initiative at the start of combat, on the result of an odd number, the wound re-opens: they suffer 1 hit point at the end of each of your turns, unless a character uses an action to bind the wound. This flaw ends if you receive magical healing, or if you succeed at a DC 17 Constitution saving throw after a long rest: if they fail this save the first time, they gain a +5 bonus in future.")])]),_c('tr',[_c('td',[_vm._v("60-63")]),_c('th',[_vm._v("Ruined Armor")]),_c('td',[_vm._v("Unless your armour is enchanted or made of adamantine, the attack leaves it badly damaged, affording little protection and fitting badly. You suffer -2 from your AC. This penalty lasts until it is replaced, repaired (costing half the the total cost of the item) or subject to a Mending spell. If you have no armour, treat this result as Minor Scar instead.")])]),_c('tr',[_c('td',[_vm._v("64-67")]),_c('th',[_vm._v("Old Battle Wound")]),_c('td',[_vm._v("You more or less recover, but become plagued for years with recurring pains. After every long rest, you must succeed at a DC 11 Constitution saving throw or suffer -2 to your Dexterity and Wisdom scores until the next long rest. This penalty can be neutralised by special salves, prepared by a character proficient in Medicine or using a herbalism kit with which they are proficient. This flaw can be removed through magic such as Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("68-70")]),_c('th',[_vm._v("Infected Wound")]),_c('td',[_vm._v("The damage refuses to properly heal, and instead becomes swollen and leaks a noisome pus. Your hit point maximum is reduced by 1 for every 24 hours the wound persists. If your hit point maximum drops to 0, you die. The wound heals if you receive magical healing or, alternatively, someone can tend to the wound and make a DC 14 Wisdom (Medicine) check once every 24 hours. After ten successes, the wound heals. Characters who are immune to disease treat this result as Minor Scar instead.")])]),_c('tr',[_c('td',[_vm._v("71-73")]),_c('th',[_vm._v("Shish Kebab")]),_c('td',[_vm._v("The attacker impales you, and the force of the blow drives the protruding tip towards another creature, too. Another creature of the attacker's choice within 5 feet (or within 10 feet, if the attacker is huge or gargantuan) is also targeted, and the attacker makes another attack roll for free. This second target does not need to be within normal attacking range for the attacker, but they will gain the benefit of cover from the impaled character! If you are huge or gargantuan, you are too heavy to be moved, and this result has no effect.")])]),_c('tr',[_c('td',[_vm._v("74-76")]),_c('th',[_vm._v("Horrible Scar")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 14 Dexterity saving throw  if you fail (or have no helmet), you are disfigured to the extent that the wound can't be easily concealed, or palated. You have disadvantage on Charisma (Persuasion) checks and advantage on Charisma (Intimidation) checks. Magic like Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("77-96")]),_c('th',[_vm._v("Minor Scar")]),_c('td',[_vm._v("The scar doesn't have any adverse effect, but may make you more memorable and badass! Magic such as Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("97-98")]),_c('th',[_vm._v("Snagged Weapon")]),_c('td',[_vm._v("As the attacker's attack strikes home, the weight and direction of your fall drags the weapon from their hand, and it is buried too deep to pull out with ease. The attacker is disarmed until they spend an action retrieving their weapon. If the weapon is a natural melee weapon (like claws or teeth) the unfortunate creature is restrained until it uses an action to free itself.")])]),_c('tr',[_c('td',[_vm._v("99+")]),_c('th',[_vm._v("I'm... I'm Alright!")]),_c('td',[_vm._v("The gods must be smiling today  the blow seemed worse than it actually is, and you are assumed dead. Rather than making any death saving throws, you are instead stabilised and returned to 1 hit point and, at the beginning of your next turn, regain 1d10 hit points and can act as normal, gaining advantage on your first attack that turn.")])])])])]),_c('b-tab',{attrs:{"index":"9","title":"Poison"}},[_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("D100 Roll")]),_c('th',[_vm._v("Injury")]),_c('th',[_vm._v("Description")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("1")]),_c('th',[_vm._v("Instant Death")]),_c('td',[_vm._v("The toxin sweeps through your system, stopping the heart cold, and killing you instantly.")])]),_c('tr',[_c('td',[_vm._v("2")]),_c('th',[_vm._v("Fatal Poisoning")]),_c('td',[_vm._v("The venom burns through your system, systematically ruining one organ after another. Even if you succeed at the normal death saving throws, you remain unconscious and will die after 1d6 turns have passed, at the end of your turn, unless you are magically healed above 0 hit points and the poison is neutralised.")])]),_c('tr',[_c('td',[_vm._v("3")]),_c('th',[_vm._v("Coma")]),_c('td',[_vm._v("You enter a death-like sleep from which nothing can seemingly awaken you, though dim life signs still remain. You remain unconscious, even after being stabilised. Following a long rest, make a death saving throw. Three failed saves result in death (possibly due to lack of fluids, amongst other complications), but a result of 15 or means the coma ends. If and when you awaken, you suffer three levels of exhaustion (these exhaustion levels can be removed normally). The coma can also be ended prematurely by magic such as Dream, Greater Restoration, Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("4")]),_c('th',[_vm._v("Shrivelled Arm")]),_c('td',[_vm._v("Your arm or hand is withered beyond recovery, and will require amputation. Following recovery, you can no longer hold anything with two hands, and can hold only a single object at a time. The distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. Finally, if the lost arm was your dominant arm (50% chance), unless you are trained in the Dual Wielder feat or the Two-Weapon Fighting Style, you suffer disadvantage with weapon attacks until you learn to use your off-hand. You learn to live with this pain and train your off-hand after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("5")]),_c('th',[_vm._v("Shrivelled Leg")]),_c('td',[_vm._v("Your leg or foot is withered beyond recovery, and will require amputation. Following recovery, your speed on foot is halved and you must use a cane or crutch to move, unless you receive a prosthesis of some kind. Additionally, you fall prone after using the dash action, and suffer disadvantage on Dexterity checks made to balance. Finally, the distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. You learn to live with this pain after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("6-7")]),_c('th',[_vm._v("Anaphylactic Shock")]),_c('td',[_vm._v("Your body reacts dramatically to the venom. Following recovery, you suffer disadvantage on saves against the effects of this specific venom. If you do become affected by this venom, as well as any other effects, you must succeed at a DC 14 Constitution saving throw or be immediately reduced to 0 hit points, and will die after 1d6 turns have passed, at the end of your turn, unless the poison is neutralised, or you are stabilised by another character succeeding at a DC 14 Wisdom (Medicine) check. This result overrides any existing resistance you may have to that venom.")])]),_c('tr',[_c('td',[_vm._v("8-11")]),_c('th',[_vm._v("Multiple Injuries")]),_c('td',[_vm._v("Roll again on this table 1d3+1 times, suffering the combined effects of the various results (within reason!). Reroll any duplicates, and also reroll Multiple Injuries, Minor Scar, Full Recovery, \"I'm... I'm Alright!\" and any results which indicate death (like Fatal Poisoning or Instant Death).")])]),_c('tr',[_c('td',[_vm._v("12-15")]),_c('th',[_vm._v("Destroyed Eyes")]),_c('td',[_vm._v("The virulent toxin destroys your eyes. From now on, you are permanently blinded, unless you are cured by magic such Greater Restoration, Heal, Regenerate or Wish. Perhaps you'll learn to adapt?")])]),_c('tr',[_c('td',[_vm._v("16-19")]),_c('th',[_vm._v("Lung Trauma")]),_c('td',[_vm._v(" Your lungs are scorched or withered, placing you dangerously close to death. Until you are stabilised, you must make two death saving throws a turn. Following recovery, even routine activity leaves you breathless, and you are wracked with coughing fits when the air is thin or smoky. For every four hours spent walking (or eight hours riding), and when reduced to half your maximum hit points or less, you must succeed at a DC 11 Constitution saving throw or suffer one level of exhaustion. Exhaustion levels gained from this flaw are lost during a short or long rest, or can be removed by a character equipped with a herbalism kit with which they are proficient. Finally, this flaw ends if you succeed at a DC 17 Constitution saving throw rolled at the beginning of every month, or if removed by magic such as Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("20-25")]),_c('th',[_vm._v("Frail")]),_c('td',[_vm._v("You recover, but your health is never quite the same again. You can barely keep food down, and lose 1 point from your Constitution score. Finally, if you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce the character's hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("26-31")]),_c('th',[_vm._v("Painful Joints")]),_c('td',[_vm._v("You heal, but are never again as sprightly. You lose 1 point from your Dexterity score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("32-37")]),_c('th',[_vm._v("Enfeebled")]),_c('td',[_vm._v("You heal, but are never again as strong. You lose 1 point from your Strength score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("38-42")]),_c('th',[_vm._v("Impaired Mind")]),_c('td',[_vm._v("Unless you are immune to poison, the venom permanently damages your mental facilities somehow. Roll 1d3: 1: you lose 1 point from your Intelligence score; 2: you lose 1 point from your Wisdom score; 3: you lose 1 point from your Charisma score. This flaw is often accompanied by speech or memory problems, requiring you to relearn basic skills and social niceties. If the same ability score is affected a second time or more in the future by this flaw, you must succeed at a DC 14 Constitution saving throw or also suffer the Coma flaw. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("43-49")]),_c('th',[_vm._v("Pelvic Injury")]),_c('td',[_vm._v("Either your pelvis is struck a crippling blow, or the attack destroys your nethers. Whenever you attempt a Strength (Athletics) check or take any action in combat (other than casting a spell), you must succeed at a DC 11 Constitution saving throw or lose 1d6 hit points. You also can't ride a horse without suffering 1d10 hit points. Finally, this injury renders you permanently infertile. This flaw ends after a base recovery time of 70 days (see Recovery above), or if you receive magic such as Heal, Regenerate or Wish, although the infertility can only be reversed by Greater Restoration or Remove Curse.")])]),_c('tr',[_c('td',[_vm._v("50-54")]),_c('th',[_vm._v("Leg Wound")]),_c('td',[_vm._v("Your leg is badly damaged. Until a base recovery time of 40 days has passed (see Recovery above), you must use a cane or crutch to move at half speed, you fall prone after using the dash action, and you suffer disadvantage on Dexterity checks made to balance. Even after the worst of the damage has passed, your speed on foot is reduced by 5 feet. The limb can be restored by Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("55-61")]),_c('th',[_vm._v("Liver Failure")]),_c('td',[_vm._v("The poison was almost too much you, and from now on you suffer vulnerability to all poison damage, and can no longer tolerate other toxins, such as alcohol. No more tavern crawls for you! This flaw overrides any existing resistance you may have to poison, and can only be removed by magic such as Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("62-68")]),_c('th',[_vm._v("Crippling Fatigue")]),_c('td',[_vm._v("You can never shake your sense of tiredness, and gain one level of exhaustion. This can be removed normally. However, following every long rest, you must succeed at a DC 11 Constitution or Wisdom saving throw (your choice), or gain a level of exhaustion. If you fail this saving throw by 5 or more, you gain two levels. Accumulating enough exhaustion levels can lead to your death. However, if you roll an unmodified 20, this flaw ends, and can also be removed by magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("69-72")]),_c('th',[_vm._v("Personality Change")]),_c('td',[_vm._v("In the aftermath of the attack, due to post-traumatic stress, you soon exhibit a shift in personality. Roll 1d100 and consult the Indefinite Madness table (on page 260 of the Dungeon Master's Guide) to see what permanent flaw you gain. This flaw can only be removed by Greater Restoration or Wish, or possibly by some kind of long-term therapy, at the Dungeon Master's discretion.")])]),_c('tr',[_c('td',[_vm._v("73-76")]),_c('th',[_vm._v("Fixation")]),_c('td',[_vm._v("Following recovery, you develop an unhealthy fixation. Roll 1d6: on a 1-3, you nurture a bitter enmity; on a 4-6, you develop an overpowering phobia. If you are immune to being frightened, then this result is bitter enmity. You should work with the Dungeon Master on the phrasing of the new flaw: your hatred or phobia may focus on some element of the attacker's identity (for example, a mindless hatred towards spiders or sorcerers) or even the type of attack (such as a fear of wine). This flaw can be removed by magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("77-96")]),_c('th',[_vm._v("Full Recovery")]),_c('td',[_vm._v("You seem to be fine.")])]),_c('tr',[_c('td',[_vm._v("97-98")]),_c('th',[_vm._v("Mithraditic Resistance")]),_c('td',[_vm._v("Your body rapidly develops a resistance to the venom that borders on the miraculous! If you succeed at a DC 20 Constitution saving throw, you are completely immune to that venom. If you fail, you instead have resistance to poison damage of the specific venom you were injured by, gain advantage on saves against its effects, and the duration of its effects on you are halved. This result overrides any existing vulnerability they may have to that venom.")])]),_c('tr',[_c('td',[_vm._v("99+")]),_c('th',[_vm._v("I'm... I'm Alright!")]),_c('td',[_vm._v("The gods must be smiling today  the blow seemed worse than it actually is, and you are assumed dead. Rather than making any death saving throws, you are instead stabilised and returned to 1 hit point and, at the beginning of your next turn, regain 1d10 hit points and can act as normal, gaining advantage on your first attack that turn.")])])])])]),_c('b-tab',{attrs:{"index":"10","title":"Psychic"}},[_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("D100 Roll")]),_c('th',[_vm._v("Injury")]),_c('th',[_vm._v("Description")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("1")]),_c('th',[_vm._v("Instant Death")]),_c('td',[_vm._v("Your brain explodes, killing you instantly.")])]),_c('tr',[_c('td',[_vm._v("2")]),_c('th',[_vm._v("Shredded Psyche")]),_c('td',[_vm._v("You convulse with agony, and your shredded mind refuses to let normal bodily processes continue. Even if you succeed at the normal death saving throws, you remain unconscious and will die after 1d6 turns have passed, at the end of your turn, unless you are magically healed above 0 hit points.")])]),_c('tr',[_c('td',[_vm._v("3")]),_c('th',[_vm._v("Coma")]),_c('td',[_vm._v("You enter a death-like sleep from which nothing can seemingly awaken you, though dim life signs still remain. You remain unconscious, even after being stabilised. Following a long rest, make a death saving throw. Three failed saves result in death (possibly due to lack of fluids, amongst other complications), but a result of 15 or means the coma ends. If and when you awaken, you suffer three levels of exhaustion (these exhaustion levels can be removed normally). The coma can also be ended prematurely by magic such as Dream, Greater Restoration, Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("4")]),_c('th',[_vm._v("Screaming Horror")]),_c('td',[_vm._v("Unless you are immune to being frightened, your mind is broken beyond redemption, and you spend your days alternating between empty-eyed stares and bouts of horrified screaming  death would be a kinder fate. You are almost entirely useless, though you can be persuaded to stumble along meekly when feeling more placid  however, there is a 50% chance (checked for after each short or long rest) that you will scream uncontrollably, unless drugged, knocked out or subject to a calm emotions spell. This flaw can only be removed by magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("5-7")]),_c('th',[_vm._v("I Can't Move!")]),_c('td',[_vm._v("Brain trauma leaves you fully paralysed, at least for a short time. You can do little more than speak, but can still cast spells that don't require somatic components. However, as you can't feel much of your body, you gain advantage on any Constitution saving throw to maintain concentration on spells due to receiving damage. After every long rest, the character can make a DC 17 Constitution saving throw to end the paralysis. This flaw can also be removed by magic such as Freedom of Movement, Greater Restoration, Lesser Restoration, Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("8-10")]),_c('th',[_vm._v("Horrors in the Dark")]),_c('td',[_vm._v("Your scrambled brain plays tricks on you, leaving your vision a deep blackness peopled by fleeting spirits and phantasms. You are temporarily blinded. This flaw ends when you receive magic such as Dream, Greater Restoration, Lesser Restoration, Remove Curse or Wish, or you succeed on a DC 14 Intelligence saving throw, which you can make after every long rest.")])]),_c('tr',[_c('td',[_vm._v("11-16")]),_c('th',[_vm._v("Brain Damage")]),_c('td',[_vm._v("The attack damages your mental facilities somehow. Roll 1d3: 1: you lose 1 point from your Intelligence score; 2: you lose 1 point from your Wisdom score; 3: you lose 1 point from your Charisma score. This flaw is often accompanied by speech or memory problems, requiring you to relearn basic skills and social niceties. If the same ability score is affected a second time or more in the future by this flaw, you must succeed at a DC 14 Constitution saving throw or also suffer the Coma flaw. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("17-21")]),_c('th',[_vm._v("Recurring Possession")]),_c('td',[_vm._v("Even if the attacker is eventually destroyed, a fragment of its spirit burrows itself deep within your psyche, making you commit horrors when you suspect yourself safe. Every time you take a long rest, you must succeed at a DC 11 Charisma saving throw or be possessed by the attacking creature (or its spirit) for up to 1d10 minutes, in a manner similar to the dominate spell. As with the spell, you can repeat your saving throw if you suffer damage. When the possession ends, you have no memory of what you have done. This possession can typically be noticed by a change in your mannerisms when possessed, and also by a detect magic spell at any time. This flaw can only be removed by magic such as Greater Restoration, Remove Curse or Wish, or possibly by destroying or confronting the possessing spirit, at the Dungeon Master's discretion.")])]),_c('tr',[_c('td',[_vm._v("22-28")]),_c('th',[_vm._v("Debilitating Migraines")]),_c('td',[_vm._v("The attack permanently damaged your brain, and you become plagued with nosebleeds and nauseating headaches. These come most frequently in times of stress, or following loud noise. When rolling for initiative at the start of combat, or when you suffer thunder damage, you must make a DC 11 Constitution saving throw. If you fail the save, the migraines come again: you suffer disadvantage on future initiative and Wisdom (Perception) checks. The migraine attack ends when you take a short or long rest. This flaw can only be removed by magic such as Greater Restoration, Heal, Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("29-33")]),_c('th',[_vm._v("Crippling Fatigue")]),_c('td',[_vm._v("You can never shake your sense of tiredness, and gain one level of exhaustion. This can be removed normally. However, following every long rest, you must succeed at a DC 11 Constitution or Wisdom saving throw (your choice), or gain a level of exhaustion. If you fail this saving throw by 5 or more, you gain two levels. Accumulating enough exhaustion levels can lead to your death. However, if you roll an unmodified 20, this flaw ends, and can also be ended by magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("34-38")]),_c('th',[_vm._v("Partially Deafened")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 11 Constitution saving throw  if you fail (or have no helmet), then either through brain damage or damage to the ears, the attack leaves your hearing permanently muffled. You suffer disadvantage on Wisdom (Perception) checks that rely on hearing. This flaw can be reversed through magic such as Greater Restoration, Heal, Lesser Restoration, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("39-48")]),_c('th',[_vm._v("Shell-Shock")]),_c('td',[_vm._v("Either due to brain damage or a lifetime of facing horrors and death, you are somewhat indecisive and slow to react. You suffer a penalty of -2 to your Initiative. This flaw can be removed through magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("49-57")]),_c('th',[_vm._v("Troubled Sleep")]),_c('td',[_vm._v("Unless you are immune to being frightened, you suffer the following flaw: though you more or less recover, your sleep or trance is ravaged by inescapable nightmares, and rarely affords much rest. You must succeed on a DC 11 Charisma or Wisdom saving throw (your choice) after every long rest, or only recover a quarter of your hit dice. If you beat the save DC by 10 or more, the flaw ends. It can also be removed by magic such as Dream, Greater Restoration, Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("58-62")]),_c('th',[_vm._v("Pushed to the Brink")]),_c('td',[_vm._v("Unless you are immune to being frightened, your mind is rendered brittle. You become over-emotional and prone to regular fits of crying, suffer disadvantage on checks or saving throws related to avoiding being frightened, and may react strongly to certain enchantment spells (at the Dungeon Master's discretion). This ends when you make a successful DC 17 Charisma or Wisdom saving throw (your choice) rolled at the beginning of each month  this saving throw can also be made if you are subject to a Calm Emotions spell. The flaw can also be removed by magic such as Greater Restoration, Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("63-71")]),_c('th',[_vm._v("Personality Change")]),_c('td',[_vm._v("In the aftermath of the attack, you soon exhibit a shift in personality. Roll 1d100 and consult the Indefinite Madness table (on page 260 of the Dungeon Master's Guide) to see what permanent flaw you gain. This flaw can only be removed by magic such as Greater Restoration or Wish, or possibly by some kind of long-term therapy, at the Dungeon Master's discretion.")])]),_c('tr',[_c('td',[_vm._v("72-76")]),_c('th',[_vm._v("Fixation")]),_c('td',[_vm._v("Following recovery, you develop an unhealthy fixation. Roll 1d6: on a 1-3, you nurture a bitter enmity; on a 4-6, you develop an overpowering phobia. If you are immune to being frightened, then this result is bitter enmity. You should work with the Dungeon Master on the phrasing of the new flaw: your hatred or phobia may focus on some element of the attacker's identity (for example, a mindless hatred towards mind flayers or sorcerers) or even the type of attack (such as a fear of magic). This flaw can be removed bymagci such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("77-96")]),_c('th',[_vm._v("Full Recovery")]),_c('td',[_vm._v("You seem to be fine.")])]),_c('tr',[_c('td',[_vm._v("97-98")]),_c('th',[_vm._v("Steel Mind")]),_c('td',[_vm._v("Though the mental onslaught is agonizing, you reconstruct your mind by will alone  stronger than it was before. From now on, you reduce psychic damage that you suffer by -3 per attack and, if attacked with psychic damage by a creature, you can use a reaction to make an opposed contest against the attacker (using your choice of Wisdom or Charisma against the attacker's Charisma). If you win the contest, the attacker suffers the same amount of psychic damage as you. Finally, if a creature attempts to read your mind, the would-be intruder suffers disadvantage on their attempt, and you gain advantage on rolls to resist.")])]),_c('tr',[_c('td',[_vm._v("99+")]),_c('th',[_vm._v("I'm... I'm Alright!")]),_c('td',[_vm._v("The gods must be smiling today  the blow seemed worse than it actually is, and you are assumed dead. Rather than making any death saving throws, you are instead stabilised and returned to 1 hit point and, at the beginning of your next turn, regain 1d10 hit points and can act as normal, gaining advantage on your first attack that turn.")])])])])]),_c('b-tab',{attrs:{"index":"11","title":"Radiant"}},[_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("D100 Roll")]),_c('th',[_vm._v("Injury")]),_c('th',[_vm._v("Description")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("1")]),_c('th',[_vm._v("Instant Death")]),_c('td',[_vm._v("Your major organs are incinerated, or your brain boils inside your skull, killing you instantly.")])]),_c('tr',[_c('td',[_vm._v("2")]),_c('th',[_vm._v("Fatal Wound")]),_c('td',[_vm._v("You suffer massive burns, or internal burn damage to major organs. Even if you succeed at the normal death saving throws, you remain unconscious and will die after 1d6 turns have passed, at the end of your turn, unless you are magically healed above 0 hit points, or stabilised with a DC 17 Wisdom (Medicine) check using a healer's kit.")])]),_c('tr',[_c('td',[_vm._v("3")]),_c('th',[_vm._v("Incinerated Arm")]),_c('td',[_vm._v("Your arm or hand is almost entirely disintegrated. Due to system shock, you must make a death saving throw immediately, with disadvantage on the roll. Following recovery, you can no longer hold anything with two hands, and can hold only a single object at a time. The distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. Finally, if the lost arm was your dominant arm (50% chance), unless you are trained in the Dual Wielder feat or the Two-Weapon Fighting Style, you suffer disadvantage with weapon attacks until you learn to use your off-hand. You learn to live with this pain and train your off-hand after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("4")]),_c('th',[_vm._v("Incinerated Leg")]),_c('td',[_vm._v("Your leg or foot is almost entirely disintegrated. Due to system shock, you must make a death saving throw immediately, with disadvantage on the roll. Following recovery, your speed on foot is halved and you must use a cane or crutch to move, unless you receive a prosthesis of some kind. Additionally, you fall prone after using the dash action, and suffer disadvantage on Dexterity checks made to balance. Finally, the distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. You learn to live with this pain after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("5-11")]),_c('th',[_vm._v("Multiple Injuries")]),_c('td',[_vm._v("Roll again on this table 1d3+1 times, suffering the combined effects of the various results (within reason!). Reroll any duplicates, and also reroll Multiple Injuries, Minor Scar, Full Recovery, \"I'm... I'm Alright!\" and any results which indicate death (like Fatal Wound or Instant Death).")])]),_c('tr',[_c('td',[_vm._v("12-16")]),_c('th',[_vm._v("Burnt-Out Eyes")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 17 Dexterity saving throw  if you fail (or have no helmet), then the intense light gives you one last glimpse of beauty, before it takes your sight forever. From now on, you are permanently blinded  unless you are cured by magic such as Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("17-20")]),_c('th',[_vm._v("Frail")]),_c('td',[_vm._v("You recover, but your health is never quite the same again. You can barely keep food down, and lose 1 point from your Constitution score. Finally, if you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce the character's hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("21-24")]),_c('th',[_vm._v("Painful Joints")]),_c('td',[_vm._v("You heal, but are never again as sprightly. You lose 1 point from your Dexterity score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("25-28")]),_c('th',[_vm._v("Enfeebled")]),_c('td',[_vm._v("You heal, but are never again as strong. You lose 1 point from your Strength score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("29-34")]),_c('th',[_vm._v("Ruined Eye")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 17 Dexterity saving throw  if you fail (or have no helmet), one of your eyes is destroyed. From now on, you suffer disadvantage on Wisdom (Perception) checks that rely on sight, and on ranged attack rolls. If a subsequent injury damages your other eye, you are permanently blinded. Magic such as Regenerate or Wish can restore the lost eye.")])]),_c('tr',[_c('td',[_vm._v("35-38")]),_c('th',[_vm._v("Scarred Eye")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 14 Dexterity saving throw  if you fail (or have no helmet), one of your eyes is scarred by the attack, costing its sight for a time. From now on, you suffer disadvantage on Wisdom (Perception) checks that rely on sight, and on ranged attack rolls. If a subsequent injury damages your other eye, you are blinded. The damaged eye's function can be restored either by succeeding at a DC 17 Constitution saving throw after a long rest, or by magic such as Greater Restoration, Heal, Lesser Restoration, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("36-42")]),_c('th',[_vm._v("Pelvic Injury")]),_c('td',[_vm._v("Either your pelvis is struck a crippling blow, or the attack destroys your nethers. Whenever you attempt a Strength (Athletics) check or take any action in combat (other than casting a spell), you must succeed at a DC 11 Constitution saving throw or lose 1d6 hit points. You also can't ride a horse without suffering 1d10 hit points. Finally, this injury renders you permanently infertile. This flaw ends after a base recovery time of 70 days (see Recovery above), or if you receive magic such as Heal, Regenerate or Wish, although the infertility can only be reversed by Greater Restoration or Remove Curse.")])]),_c('tr',[_c('td',[_vm._v("43-46")]),_c('th',[_vm._v("Leg Wound")]),_c('td',[_vm._v("Your leg is badly damaged. Until a base recovery time of 40 days has passed (see Recovery above), you must use a cane or crutch to move at half speed, you fall prone after using the dash action, and you suffer disadvantage on Dexterity checks made to balance. Even after the worst of the damage has passed, your speed on foot is reduced by 5 feet. The limb can be restored by either Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("47-50")]),_c('th',[_vm._v("Hand Injury")]),_c('td',[_vm._v("The attack costs you several fingers. There is a 50% chance of the affected hand being your dominant hand. You can just about carry a shield, but if it's a weapon hand, your weapon attacks suffer a -2 penalty, unless you are trained in either the Dual Wielder feat or the Two-Weapon Fighting Style. Also, if it is your dominant hand, if you attempt to cast a spell with somatic components, you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. These penalties last a base recovery time of 40 days (see Recovery above). This flaw can be removed by magic such as Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("51-54")]),_c('th',[_vm._v("Ruined Armour")]),_c('td',[_vm._v("Unless your armour is enchanted or made of adamantine, the attack leaves it badly damaged, affording little protection and fitting badly. You suffer -2 from your AC. This penalty lasts until it is replaced, repaired (costing half the the total cost of the item) or subject to a Mending spell. If you have no armour, treat this result as Minor Scar instead.")])]),_c('tr',[_c('td',[_vm._v("55-58")]),_c('th',[_vm._v("Old Battle Wound")]),_c('td',[_vm._v("You more or less recover, but become plagued for years with recurring pains. After every long rest, you must succeed at a DC 11 Constitution saving throw or suffer -2 to your Dexterity and Wisdom scores until the next long rest. This penalty can be neutralised by special salves, prepared by a character proficient in Medicine or using a herbalism kit with which they are proficient. This flaw can be removed through magic such as Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("59-62")]),_c('th',[_vm._v("Useless Shield")]),_c('td',[_vm._v("Unless your shield is enchanted or made of adamantine, the attack leaves it ruined and worthless. If you don't have a shield, treat this result as Ruined Armour.")])]),_c('tr',[_c('td',[_vm._v("63-66")]),_c('th',[_vm._v("Blinding Refraction")]),_c('td',[_vm._v("The focused beam not only puts you down, but also, by sheer chance, reflects off a shiny surface to strike another creature. A random creature without total cover from you (possibly including the attacker) must succeed at a DC14 Dexterity saving throw (this roll is made with advantage if they have half or three-quarters cover), or be blinded until the end of their next turn.")])]),_c('tr',[_c('td',[_vm._v("67-71")]),_c('th',[_vm._v("Fixation")]),_c('td',[_vm._v("Following recovery, you develop an unhealthy fixation. Roll 1d6: on a 1-3, you nurture a bitter enmity; on a 4-6, you develop an overpowering phobia. If you are immune to being frightened, then this result is bitter enmity. You should work with the Dungeon Master on the phrasing of the new flaw: your hatred or phobia may focus on some element of the attacker's identity (for example, a mindless hatred towards angels or sorcerers) or even the type of attack (such as a fear of sunlight). This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("72-76")]),_c('th',[_vm._v("Horrible Scar")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 14 Dexterity saving throw  if you fail (or have no helmet), you are disfigured to the extent that the wound can't be easily concealed, or palated. You have disadvantage on Charisma (Persuasion) checks and advantage on Charisma (Intimidation) checks. Magic like Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("77-96")]),_c('th',[_vm._v("Minor Scar")]),_c('td',[_vm._v("The scar doesn't have any adverse effect, but may make you more memorable and badass! Magic such as Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("97-98")]),_c('th',[_vm._v("Sun-Blessed")]),_c('td',[_vm._v("Either due to some purity inherent in your nature, or as part of an unknowable divine plan, the holy radiance imbues itself permanently into your mortal shell. Your skin or eyes seem to glimmer with astral radiance, and you can use a bonus action to cause your skin to glow with a soft light, shedding bright light in a 5-foot radius and dim light for an additional 5 feet  this can be turned off with another bonus action, and also turns off if you fall unconscious. Additionally, once per long rest, you can cast the bless spell as a 1st level spell, without using up a spell slot. This flaw can only be removed by magic such as Protection from Evil and Good or Wish.")])]),_c('tr',[_c('td',[_vm._v("99+")]),_c('th',[_vm._v("I'm... I'm Alright!")]),_c('td',[_vm._v("The gods must be smiling today  the blow seemed worse than it actually is, and you are assumed dead. Rather than making any death saving throws, you are instead stabilised and returned to 1 hit point and, at the beginning of your next turn, regain 1d10 hit points and can act as normal, gaining advantage on your first attack that turn.")])])])])]),_c('b-tab',{attrs:{"index":"12","title":"Slashing"}},[_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("D100 Roll")]),_c('th',[_vm._v("Injury")]),_c('th',[_vm._v("Description")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("1")]),_c('th',[_vm._v("Instant Death")]),_c('td',[_vm._v("You are decapitated, or suffer some other grievous wound which kills you instantly.")])]),_c('tr',[_c('td',[_vm._v("2")]),_c('th',[_vm._v("Fatal Wound")]),_c('td',[_vm._v("Either you are gutted, your spine is severed, or something equally horrible happens. Even if you succeed at the normal death saving throws, you remain unconscious and will die after 1d6 turns have passed, at the end of your turn, unless you are magically healed above 0 hit points, or stabilised with a DC 17 Wisdom (Medicine) check using a healer's kit.")])]),_c('tr',[_c('td',[_vm._v("3")]),_c('th',[_vm._v("Disembowelment")]),_c('td',[_vm._v("Your internal organs become external. Even if you succeed at the normal death saving throws and regain consciousness, you are capable of nothing more strenuous than moving at half speed (rounded up), cannot take actions, bonus actions or reactions, and will die after a number of hours equal to your Constitution score, unless you are subject to powerful healing like Heal, Regenerate or Wish. Alternatively, if a character spends a use of a healer's kit and succeeds at a DC 17 Wisdom (Medicine) check, then you will be stabilised and able to undertake normal activity after a long rest.")])]),_c('tr',[_c('td',[_vm._v("4")]),_c('th',[_vm._v("Paralysis")]),_c('td',[_vm._v("Spinal damage leaves you unable to move or feel anything from the waist down. You will need carrying or some sort of device to allow mobility (otherwise you can only crawl 5 feet per turn), and count as restrained. Still, there is always hope for a particularly strong person: at the beginning of every week, you can make a DC 20 Constitution saving throw  on a success, you miraculously recover. Otherwise, this flaw only ends if you receive powerful magic such as Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("5")]),_c('th',[_vm._v("Severed Arm")]),_c('td',[_vm._v("Your arm or hand is hacked off, pumping arterial blood everywhere. Due to the flow of blood, you lose 1d6 hit points at the end of each of your turns, unless you receive magical healing to seal the stump, it is cauterised by fire or radiant damage, or another character uses an action to apply a tourniquet. Due to system shock, you must make a death saving throw immediately, with disadvantage on the roll. Following recovery, you can no longer hold anything with two hands, and can hold only a single object at a time. The distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. Finally, if the lost arm was your dominant arm (50% chance), unless you are trained in the Dual Wielder feat or the Two-Weapon Fighting Style, you suffer disadvantage with weapon attacks until you learn to use your off-hand. You learn to live with this pain and train your off-hand after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("6")]),_c('th',[_vm._v("Severed Leg")]),_c('td',[_vm._v("Your leg or foot is hacked off, pumping arterial blood everywhere. Due to the flow of blood, you lose 1d6 hit points at the end of each of your turns, unless you receive magical healing to seal the stump, it is cauterised by fire or radiant damage, or another character uses an action to apply a tourniquet. Due to system shock, you must make a death saving throw immediately, with disadvantage on the roll. Following recovery, your speed on foot is halved and you must use a cane or crutch to move, unless you receive a prosthesis of some kind. Additionally, you fall prone after using the dash action, and suffer disadvantage on Dexterity checks made to balance. Finally, the distraction of the pain means that if you attempt to cast any spell you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. You learn to live with this pain after a base recovery time of 40 days (see Recovery above). The limb can be restored by either Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("7-11")]),_c('th',[_vm._v("Multiple Injuries")]),_c('td',[_vm._v("Roll again on this table 1d3+1 times, suffering the combined effects of the various results (within reason!). Reroll any duplicates, and also reroll Multiple Injuries, Minor Scar, Full Recovery, \"I'm... I'm Alright!\" and any results which indicate death (like Disembowelment, Fatal Wound or worse).")])]),_c('tr',[_c('td',[_vm._v("12-15")]),_c('th',[_vm._v("I Can't Move!")]),_c('td',[_vm._v("Brain trauma leaves you fully paralysed, at least for a short time. You can do little more than speak, but can still cast spells that don't require somatic components. However, as you can't feel much of your body, you gain advantage on any Constitution saving throw to maintain concentration on spells due to receiving damage. After every long rest, the character can make a DC 17 Constitution saving throw to end the paralysis. This flaw can also be ended by magic such as Freedom of Movement, Greater Restoration, Lesser Restoration, Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("16-19")]),_c('th',[_vm._v("Frail")]),_c('td',[_vm._v("You recover, but your health is never quite the same again. You can barely keep food down, and lose 1 point from your Constitution score. Finally, if you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce the character's hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("20-23")]),_c('th',[_vm._v("Painful Joints")]),_c('td',[_vm._v("You heal, but are never again as sprightly. You lose 1 point from your Dexterity score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("24-27")]),_c('th',[_vm._v("Enfeebled")]),_c('td',[_vm._v("You heal, but are never again as strong. You lose 1 point from your Strength score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("28-31")]),_c('th',[_vm._v("Head Injury")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 11 Constitution saving throw  if you fail (or have no helmet), ten head trauma damages your mental facilities somehow, often leaving a bald patch at the scar. Roll 1d3: 1: you lose 1 point from your Intelligence score; 2: you lose 1 point from your Wisdom score; 3: you lose 1 point from your Charisma score. This flaw is often accompanied by speech or memory problems, requiring you to relearn basic skills and social niceties. If the same ability score is affected a second time or more in the future by this flaw, you must succeed at a DC 14 Constitution saving throw or also suffer the Coma flaw. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("32-35")]),_c('th',[_vm._v("Broken Arm")]),_c('td',[_vm._v("As the attack slams home, an audible 'crack' renders an arm close to useless for a time. There is a 50% chance that the broken arm is your dominant arm and if so, unless you are trained in the Dual Wielder feat or the Two-Weapon Fighting Style, you suffer disadvantage with weapon attacks. If it is your other arm, you cannot wear a shield. This flaw ends after a base recovery time of 30 weeks (see Recovery above), or if you receive magic such as a Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("36-39")]),_c('th',[_vm._v("Leg Wound")]),_c('td',[_vm._v("Your leg is badly damaged. Until a base recovery time of 40 days has passed (see Recovery above), you must use a cane or crutch to move at half speed, you fall prone after using the dash action, and you suffer disadvantage on Dexterity checks made to balance. Even after the worst of the damage has passed, your speed on foot is reduced by 5 feet. The limb can be restored by magic such as Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("40-43")]),_c('th',[_vm._v("Broken Ribs")]),_c('td',[_vm._v("Your rib cage is partly caved in. Whenever you attempt a Strength (Athletics) check or attempt to take any action in combat (other than casting a spell), you must succeed at a DC 11 Constitution saving throw. If you fail the save, you lose your action and can't use reactions until the start of your next turn. This flaw ends after a base recovery time of 50 days (see Recovery above), or if you receive magic such as Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("44-47")]),_c('th',[_vm._v("Hand Injury")]),_c('td',[_vm._v("The attack costs you several fingers. There is a 50% chance of the affected hand being your dominant hand. You can just about carry a shield, but if it's a weapon hand, your weapon attacks suffer a -2 penalty, unless you are trained in either the Dual Wielder feat or the Two-Weapon Fighting Style. Also, if it is your dominant hand, if you attempt to cast a spell with somatic components, you must first succeed on a DC 11 Constitution saving throw or lose your action that turn  the spell slot, if any, is not lost. These penalties last a base recovery time of 40 days (see Recovery above). This flaw can be removed by magic such as Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("48-51")]),_c('th',[_vm._v("Scalp Cut")]),_c('td',[_vm._v("Unless you are wearing a helmet, you suffer the following flaw: it's only a shallow wound, but that doesn't seem to stop it bleeding profusely, and blinding you with your own blood. When you roll for initiative at the start of combat, on the result of an odd number, you are blinded. You can use a bonus action to wipe away the blood to avoid being blinded until the beginning of your next turn. This flaw ends either by receiving magical healing, or after a long rest.")])]),_c('tr',[_c('td',[_vm._v("52-55")]),_c('th',[_vm._v("Bleeding Wound")]),_c('td',[_vm._v("Frustratingly, the latest injury is loathe to stop bleeding. When you roll for initiative at the start of combat, on the result of an odd number, the wound re-opens: you suffer 1 damage at the end of each of your turns, unless a character uses an action to bind the wound. This flaw ends if you receive magical healing, or if you succeed at a DC 17 Constitution saving throw after a long rest: if you fail this save the first time, you gain a +5 bonus in future.")])]),_c('tr',[_c('td',[_vm._v("56-60")]),_c('th',[_vm._v("Ruined Armor")]),_c('td',[_vm._v("Unless your armour is enchanted or made of adamantine, the attack leaves it badly damaged, affording little protection and fitting badly. You suffer -2 from your AC. This penalty lasts until it is replaced, repaired (costing half the the total cost of the item) or subject to a Mending spell. If you have no armour, treat this result as Minor Scar instead.")])]),_c('tr',[_c('td',[_vm._v("61-64")]),_c('th',[_vm._v("Useless Shield")]),_c('td',[_vm._v("Unless your shield is enchanted or made of adamantine, the attack leaves it ruined and worthless. If you don't have a shield, treat this result as Ruined Armour instead.")])]),_c('tr',[_c('td',[_vm._v("65-68")]),_c('th',[_vm._v("Infected Wounds")]),_c('td',[_vm._v("The damage refuses to properly heal, and instead becomes swollen and leaks a noisome pus. Your hit point maximum is reduced by 1 for every 24 hours the wound persists. If your hit point maximum drops to 0, you die. The wound heals if you receive magical healing or, alternatively, someone can tend to the wound and make a DC 14 Wisdom (Medicine) check once every 24 hours. After ten successes, the wound heals. Characters who are immune to disease treat this result as Minor Scar instead.")])]),_c('tr',[_c('td',[_vm._v("69-72")]),_c('th',[_vm._v("Fixation")]),_c('td',[_vm._v("Following recovery, you develop an unhealthy fixation. Roll 1d6: on a 1-3, you nurture a bitter enmity; on a 4-6, you develop an overpowering phobia. If you are immune to being frightened, then this result is bitter enmity. You should work with the Dungeon Master on the phrasing of the new flaw: your hatred or phobia may focus on some element of the attacker's identity (for example, a mindless hatred towards orcs or sorcerers) or even the type of attack (such as a fear of knives). This flaw can be removed by magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("73-76")]),_c('th',[_vm._v("Horrible Scar")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 14 Dexterity saving throw  if you fail (or have no helmet), you are disfigured to the extent that the wound can't be easily concealed, or palated. You have disadvantage on Charisma (Persuasion) checks and advantage on Charisma (Intimidation) checks. Magic like Heal, Regenerate or Wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("77-98")]),_c('th',[_vm._v("Minor Scar")]),_c('td',[_vm._v("The scar doesn't have any adverse effect, but may make you more memorable and badass! Magic such as Heal, Regenerate or wish can remove the scar.")])]),_c('tr',[_c('td',[_vm._v("99+")]),_c('th',[_vm._v("I'm... I'm Alright!")]),_c('td',[_vm._v("The gods must be smiling today  the blow seemed worse than it actually is, and you are assumed dead. Rather than making any death saving throws, you are instead stabilised and returned to 1 hit point and, at the beginning of your next turn, regain 1d10 hit points and can act as normal, gaining advantage on your first attack that turn.")])])])])]),_c('b-tab',{attrs:{"index":"13","title":"Thunder"}},[_c('table',{staticClass:"table table-striped"},[_c('thead',[_c('tr',[_c('th',[_vm._v("D100 Roll")]),_c('th',[_vm._v("Injury")]),_c('th',[_vm._v("Description")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("1")]),_c('th',[_vm._v("Instant Death")]),_c('td',[_vm._v("Your skull is shattered, or you suffer massive internal injuries, killing you instantly.")])]),_c('tr',[_c('td',[_vm._v("2")]),_c('th',[_vm._v("Fatal Wound")]),_c('td',[_vm._v("Either your back is broken, or your internal organs are ruptured. Even if you succeed at the normal death saving throws, you remain unconscious and will die after 1d6 turns have passed, at the end of your turn, unless you are magically healed above 0 hit points, or stabilised with a DC 17 Wisdom (Medicine) check using a healer's kit.")])]),_c('tr',[_c('td',[_vm._v("3")]),_c('th',[_vm._v("Paralysis")]),_c('td',[_vm._v("Spinal damage leaves you unable to move or feel anything from the waist down. You will need carrying or some sort of device to allow mobility (otherwise you can only crawl 5 feet per turn), and count as restrained. Still, there is always hope for a particularly strong person: at the beginning of every week, you can make a DC 20 Constitution saving throw  on a success, you miraculously recover. Otherwise, this flaw only ends if you receive magic such as Greater Restoration, Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("4")]),_c('th',[_vm._v("Coma")]),_c('td',[_vm._v("You enter a death-like sleep from which nothing can seemingly awaken you, though dim life signs still remain. You remain unconscious, even after being stabilised. Following a long rest, make a death saving throw. Three failed saves result in death (possibly due to lack of fluids, amongst other complications), but a result of 15 or means the coma ends. If and when you awaken, you suffer three levels of exhaustion (these exhaustion levels can be removed normally). The coma can also be ended prematurely by magic such as Dream, Greater Restoration, Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("5-11")]),_c('th',[_vm._v("Multiple Injuries")]),_c('td',[_vm._v("Roll again on this table 1d3+1 times, suffering the combined effects of the various results (within reason!). Reroll any duplicates, and also reroll Multiple Injuries, Minor Scar, Full Recovery, \"I'm... I'm Alright!\" and any results which indicate death (like Fatal Wound or Instant Death).")])]),_c('tr',[_c('td',[_vm._v("12-14")]),_c('th',[_vm._v("Frail")]),_c('td',[_vm._v("You recover, but your health is never quite the same again. You can barely keep food down, and lose 1 point from your Constitution score. Finally, if you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce the character's hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("15-17")]),_c('th',[_vm._v("Painful Joints")]),_c('td',[_vm._v("You heal, but are never again as sprightly. You lose 1 point from your Dexterity score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("18-20")]),_c('th',[_vm._v("Enfeebled")]),_c('td',[_vm._v("You heal, but are never again as strong. You lose 1 point from your Strength score. If you suffer this flaw again in the future, your hit dice are permanently reduced by one: if multiple versions of this flaw reduce your hit dice to 0, you die. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("21-25")]),_c('th',[_vm._v("Head Injury")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 11 Constitution saving throw  if you fail (or have no helmet), ten head trauma damages your mental facilities somehow, often leaving a bald patch at the scar. Roll 1d3: 1: you lose 1 point from your Intelligence score; 2: you lose 1 point from your Wisdom score; 3: you lose 1 point from your Charisma score. This flaw is often accompanied by speech or memory problems, requiring you to relearn basic skills and social niceties. If the same ability score is affected a second time or more in the future by this flaw, you must succeed at a DC 14 Constitution saving throw or also suffer the Coma flaw. This flaw can be removed by Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("26-33")]),_c('th',[_vm._v("Deafened")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 14 Constitution saving throw  if you fail (or have no helmet), then either through brain damage or damage to the ears, the attack leaves you deafened. This flaw is removed if you receive magic such as Greater Restoration, Heal, Lesser Restoration, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("34-37")]),_c('th',[_vm._v("Broken Ribs")]),_c('td',[_vm._v("Your rib cage is partly caved in. Whenever you attempt a Strength (Athletics) check or attempt to take any action in combat (other than casting a spell), you must succeed at a DC 11 Constitution saving throw. If you fail the save, you lose your action and can't use reactions until the start of your next turn. This flaw ends after a base recovery time of 50 days (see Recovery above), or if you receive magic such as Heal, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("38-43")]),_c('th',[_vm._v("Debilitating Migraines")]),_c('td',[_vm._v("The attack permanently damaged your brain, and you become plagued with nosebleeds and nauseating headaches. These come most frequently in times of stress, or following loud noise. When rolling for initiative at the start of combat, or when you are hit by an attack causing thunder damage, you must make a DC 11 Constitution saving throw. If you fail the save, the migraines come again: you suffer disadvantage on future initiative and Wisdom (Perception) checks. The migraine attack ends when you take a short or long rest. This flaw can only be removed by magic such as Greater Restoration, Heal, Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("44-50")]),_c('th',[_vm._v("Punch Drunk")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 11 Constitution saving throw  if you fail (or have no helmet), then due to hearing impairment or brain trauma, your coordination is often disrupted. After every short or long rest, you must succeed at a DC 11 Constitution or Wisdom saving throw (your choice), or suffer disadvantage on Dexterity (Acrobatics) checks. This penalty lasts until the next short or long rest. Additionally, if you are knocked prone, it costs you double the standard movement to stand up. This flaw can be removed through magic such as Greater Restoration, Heal, Lesser Restoration, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("51-57")]),_c('th',[_vm._v("Partially Deafened")]),_c('td',[_vm._v("If you wear a helmet, you must make a DC 11 Constitution saving throw  if you fail (or have no helmet), then either through brain damage or damage to the ears, the attack leaves your hearing permanently muffled. You suffer disadvantage on Wisdom (Perception) checks that rely on hearing. This flaw can be removed through magic such as Greater Restoration, Heal, Lesser Restoration, Regenerate or Wish.")])]),_c('tr',[_c('td',[_vm._v("58-62")]),_c('th',[_vm._v("Shell-Shock")]),_c('td',[_vm._v("Either due to brain damage or a lifetime of facing horrors and death, you are somewhat indecisive and slow to react. You suffer a penalty of -2 to your Initiative. This flaw can be removed through magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("63-67")]),_c('th',[_vm._v("Troubled Sleep")]),_c('td',[_vm._v("Unless you are immune to being frightened, you suffer the following flaw: though you more or less recover, your sleep or trance is ravaged by inescapable nightmares, and rarely affords much rest. You must succeed on a DC 11 Charisma or Wisdom saving throw (your choice) after every long rest, or only recover a quarter of your hit dice. If you beat the save DC by 10 or more, the flaw ends. It can also be removed by magic such as Dream, Greater Restoration, Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("68-70")]),_c('th',[_vm._v("Personality Change")]),_c('td',[_vm._v("In the aftermath of the attack, either as a result of head injury or due to post-traumatic stress, you soon exhibit a shift in personality. Roll 1d100 and consult the Indefinite Madness table (on page 260 of the Dungeon Master's Guide) to see what permanent flaw you gain. This flaw can only be removed by magic such as Greater Restoration or Wish, or possibly by some kind of long-term therapy, at the Dungeon Master's discretion.")])]),_c('tr',[_c('td',[_vm._v("71-73")]),_c('th',[_vm._v("Shrapnel")]),_c('td',[_vm._v("As the sound booms around you, its tones shatter nearby fragile objects (such as windows or a non-magic metal weapon), and the shards slice into an unfortunate witness. A random creature (possibly including the attacker) within 30 feet of you must succeed on a DC 14 Dexterity saving throw, or suffer 1d6 piercing damage; this damage may be higher at the Dungeon Master's discretion, depending on the size and weight of the shattered material.")])]),_c('tr',[_c('td',[_vm._v("74-76")]),_c('th',[_vm._v("Fixation")]),_c('td',[_vm._v("Following recovery, you develop an unhealthy fixation. Roll 1d6: on a 1-3, you nurture a bitter enmity; on a 4-6, you develop an overpowering phobia. If you are immune to being frightened, then this result is bitter enmity. You should work with the Dungeon Master on the phrasing of the new flaw: your hatred or phobia may focus on some element of the attacker's identity (for example, a mindless hatred towards elementals or sorcerers) or even the type of attack (such as a fear of storms). This flaw can be removed by magic such as Greater Restoration or Wish.")])]),_c('tr',[_c('td',[_vm._v("77-96")]),_c('th',[_vm._v("Full Recovery")]),_c('td',[_vm._v("You seem to be fine.")])]),_c('tr',[_c('td',[_vm._v("97-98")]),_c('th',[_vm._v("Thunder-Born")]),_c('td',[_vm._v("By some freak coincidence, the intensity of the sound opens the briefest of rifts to the Plane of Elemental Air and you become permanently infused with the smallest fragment of energy from that plane. In moments of anger or excitement, ambient thunder rolls protectively around you. You can use a reaction to gain resistance to thunder and lightning damage until the beginning of your next turn, but a growling wave of thunder sweeps out, audible up to 100 feet away, and creatures within 5 feet must succeed at a DC 14 Constitution saving throw or suffer 1d6 thunder damage. Finally, when you spend more than three nights in one area, it is likely that natural storms will begin to form in the vicinity. This flaw can only be removed by magic such as Remove Curse or Wish.")])]),_c('tr',[_c('td',[_vm._v("99+")]),_c('th',[_vm._v("I'm... I'm Alright!")]),_c('td',[_vm._v("The gods must be smiling today  the blow seemed worse than it actually is, and you are assumed dead. Rather than making any death saving throws, you are instead stabilised and returned to 1 hit point and, at the beginning of your next turn, regain 1d10 hit points and can act as normal, gaining advantage on your first attack that turn.")])])])])])],1)],1),_c('b-tab',{attrs:{"index":"3","title":"Actions"}},[_c('h3',[_vm._v("Actions")]),_c('h4',[_vm._v("Movement")]),_c('table',{staticClass:"table table-striped datatable"},[_c('thead',[_c('tr',[_c('th',[_vm._v("Item")]),_c('th',[_vm._v("Rule")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("Difficult Terrain")]),_c('td',[_vm._v("Costs 1 extra foot of movement per foot moved(stacks with other modifiers). Low furniture, rubble, undergrowth, steep stairs, snow, shallow bogs, and other creatures spaces are difficult terrain")])]),_c('tr',[_c('td',[_vm._v("Standing Up")]),_c('td',[_vm._v("Costs an amount of movement equal to half your speed (rounded down).  Cannot stand if speed is 0.")])]),_c('tr',[_c('td',[_vm._v("Crawling")]),_c('td',[_vm._v("Costs 1 extra foot of movement per foot moved(stacks with other modifiers).")])]),_c('tr',[_c('td',[_vm._v("Moving through another creature's space")]),_c('td',[_vm._v("Can always move through friendly creatures space.  Hostile creatures only if 2 sizes larger.  Cannot end movement in another creatures space. Always difficult terrain.")])]),_c('tr',[_c('td',[_vm._v("Flying")]),_c('td',[_vm._v("No minimum distance moved.  Without hover you fall if you are knocked prone or have your speed reduced to 0.")])]),_c('tr',[_c('td',[_vm._v("Squeezing")]),_c('td',[_vm._v("Must be large enough for creature 1 size smaller.  Must spend 1 extra foot of movement for every foot (stacks with other modifiers).")])]),_c('tr',[_c('td',[_vm._v("Climbing & Swimming")]),_c('td',[_vm._v("Must spend 1 extra foot of movement for every foot moved (stacks with other modifiers), unless you have a specific speed for climbing or swimming.")])]),_c('tr',[_c('td',[_vm._v("Alternating between types of movement.")]),_c('td',[_vm._v("Subtract the total distance you have moved from whatever speed you are currently using to determine how far you can continue moving.")])]),_c('tr',[_c('td',[_vm._v("Running Jump")]),_c('td',[_vm._v("Horizontal feet equal to strength score, Vertical feet equal to 3 + strength modifier. Minimum 10 ft. on foot before jump.")])]),_c('tr',[_c('td',[_vm._v("Standing Jump")]),_c('td',[_vm._v("Horizontal feet equal to strength score/2 Vertical feet equal to (3 + strength modifier)/2")])]),_c('tr',[_c('td',[_vm._v("Vertical Reach")]),_c('td',[_vm._v("Equal to 1 1/2 * height + vertical jumping distance.")])]),_c('tr',[_c('td',[_vm._v("Moving Stealthily")]),_c('td',[_vm._v("Requires moving at half speed.  Make a dexterity (stealth) check opposed by wisdom (perception) or passive perception of observers.")])]),_c('tr',[_c('td',[_vm._v("Mounting and Dismounting")]),_c('td',[_vm._v("Once during your move, you can mount a creature that is within 5 feet o f you or dismount. Doing so costs an amount o f movement equal to half your speed.")])]),_c('tr',[_c('td',[_vm._v("Falling off your mount")]),_c('td',[_vm._v("if your mount is moved against it's will or you are knocked prone, you must succeed on a DC 10 Dexterity saving throw or fall off the mount, landing prone in a space within 5 feet of it.  If your mount is knocked prone you may use your reaction to land on your feet next to it, otherwise you are knocked prone.")])])])]),_c('h4',[_vm._v("Actions in Combat")]),_c('table',{staticClass:"table table-striped datatable"},[_c('thead',[_c('tr',[_c('th',[_vm._v("Action")]),_c('th',[_vm._v("Rule")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("Attack")]),_c('td',[_vm._v("Make one melee or ranged weapon attack.")])]),_c('tr',[_c('td',[_vm._v("Cast a Spell")]),_c('td',[_vm._v("Most spells cost 1 action to cast.  Spell description may specify otherwise.")])]),_c('tr',[_c('td',[_vm._v("Dash")]),_c('td',[_vm._v("Gain extra movement for the turn equal to your speed.")])]),_c('tr',[_c('td',[_vm._v("Disengage")]),_c('td',[_vm._v("Movement doesn't provoke opportunity attacks for the rest of the turn.")])]),_c('tr',[_c('td',[_vm._v("Dodge")]),_c('td',[_vm._v("Attack rolls against you have disadvantage.")])]),_c('tr',[_c('td',[_vm._v("Help")]),_c('td',[_vm._v("Give advantage on the next ability check or attack roll you are helping with before the start of your next turn.")])]),_c('tr',[_c('td',[_vm._v("Hide")]),_c('td',[_vm._v("Make a dexterity (stealth) check to attempt to hide.  Cannot hide from creature that can see you.")])]),_c('tr',[_c('td',[_vm._v("Ready")]),_c('td',[_vm._v("Choose circumstance to trigger action and action to use.  Can choose at circumstance happening what you do.")])]),_c('tr',[_c('td',[_vm._v("Search")]),_c('td',[_vm._v("Try to find something using Wisdom (Perception) or Intelligence (Investigation)")])]),_c('tr',[_c('td',[_vm._v("Use an Object")]),_c('td',[_vm._v("\n              Most objects can be interacted with as part of another action(such as drawing a sword or spell component). Interact with one normal object for free each turn.  Certain objects require an action to use, or you can interact with a second object on your turn as an action.\n            ")])])])]),_c('h4',[_vm._v("Attacks")]),_c('table',{staticClass:"table table-striped datatable"},[_c('thead',[_c('tr',[_c('th',[_vm._v("Attack")]),_c('th',[_vm._v("Rule")])])]),_c('tbody',[_c('tr',[_c('td',[_vm._v("Critical Hit")]),_c('td',[_vm._v("when the d20 roll is a 20 attack hits regardless of modifiers or targets AC. Double all damage dice.")])]),_c('tr',[_c('td',[_vm._v("Critical Miss")]),_c('td',[_vm._v("when the d20 roll is a 1 the attack misses regardless